import {
    GET_TEAM,
    RESP_GET_TEAM,
    GET_TEAM_CHILD,
    RESP_GET_TEAM_CHILD
  } from "./actionTypes"

  const INIT_STATE = {
    respGetteam: {},
    respGetteamChild: {},
  }

  const teamReducer = (state = INIT_STATE, action) => {
    
    switch (action.type) {
      case GET_TEAM:
        return {
          ...state,
        }
      case RESP_GET_TEAM:
        return {
          ...state,
          respGetteam: action.payload,
        }
        case GET_TEAM_CHILD:
          return {
            ...state,
          }
        case RESP_GET_TEAM_CHILD:
          return {
            ...state,
            respGetteamChild: action.payload,
          }
      default:
        return state
    }
  }
  
  export default teamReducer