import React, { useState, useEffect } from "react";
import MetaTags from "react-meta-tags"; 

import BootstrapTable from "react-bootstrap-table-next";
import paginationFactory from "react-bootstrap-table2-paginator";
import { useFormik } from "formik";
import * as Yup from "yup";
import Breadcrumbs from "../../components/Common/Breadcrumb";

import { useSelector, useDispatch } from "react-redux"
import DeleteModal from "../../common/DeleteModal";
import '../../config';

import {
  Row,
  Col,
  Card,
  CardBody,
  Container,
  Button,
  UncontrolledTooltip,
  Label,
  Input,
  FormFeedback,
  Form,
  Spinner,
  FormGroup,
  CardHeader,
  UncontrolledAlert,
  } from "reactstrap";

  import { getCompanyData, editCompany, saveCompany, deleteCompany, resetMessage } from "../../store/app005/actions"



const Company = () => { 

    const dispatch = useDispatch();  
    const [app005p01firstRenderDone, setApp005p01firstRenderDone] = useState(false);
    const [app005p01Page, setApp005p01Page] = useState(true)
    const [app005p02Page, setApp005p02Page] = useState(false)
    const [app005p03Page, setApp005p03Page] = useState(false)
    const [app005Msg, setApp005setMsg] = useState("")
    const [app005deleteModal, setApp005DeleteModal] = useState(false);

    useEffect(() => {
      dispatch(resetMessage());
    }, [dispatch])
  
    const app005CloseAllert = () => {
      setApp005setMsg("")
    }
    
//p01
    const [app005p01TabelSearch, setApp005p01TabelSearch] = useState('');
    const [app005p01TabelCompanyReq, setApp005p01TabelCompanyReq] = useState({page : 1, limit: 10, offset: 0, sort: "companyCode", order: "asc", search: {any : app005p01TabelSearch}});
    const app005p01hendleTableChange = (type, {page, sortField, sortOrder, sizePerPage}) => {   
      if(type === "sort"){
        setApp005p01TabelCompanyReq({page : 1, limit: sizePerPage, offset: 0, sort: sortField, order: sortOrder, search: {any : app005p01TabelSearch}});
      }
      if(type==="pagination"){
        setApp005p01TabelCompanyReq({page : page, limit: sizePerPage, offset: ((page-1) * sizePerPage), sort: sortField, order: sortOrder, search: {any : app005p01TabelSearch}});
      }
    };
    useEffect(() => {
      setApp005p01firstRenderDone(true);
    }, [])

    useEffect(() => {
      setApp005p01TabelCompanyReq({page : 1, limit: 10, offset: 0, sort: "companyCode", order: "asc", search: {any : app005p01TabelSearch}});
  }, [app005p01TabelSearch])

  useEffect(() => {
    if(app005p01firstRenderDone){
      dispatch(getCompanyData(app005p01TabelCompanyReq))
    }
  }, [app005p01TabelCompanyReq])

  const app005p01CompanyData  = useSelector(state => {
    return state.CompanyReducer.respGetComp;
  });


  useEffect(() => {
    if(app005p01CompanyData.status == "0"){
      setApp005setMsg(app005p01CompanyData)
    }
  }, [app005p01CompanyData])

  const app005p01TabelCompanyColumns = [
    {
      dataField: "companyCd",
      text: "Company Code",
      sort: true,
      headerStyle: {textAlign: 'center'},
    },
    {
      dataField: "companyNm",
      text: "Company Name",
      sort: true,
      headerStyle: {textAlign: 'center'},
    },
    {
      dataField: "companyDesc",
      text: "Company Description",
      sort: true,
      headerStyle: {textAlign: 'center'},
    },
    {
      dataField: "action",
      isDummyField: true,
      text: "Action",
      headerStyle: {textAlign: 'center'},
      formatter: (cellContent, app005p01CompanyData) => (
        <>
          <div style={{justifyContent : 'center'}} className="d-flex gap-3">
              <i className="mdi mdi-pencil font-size-18  text-primary" id="edittooltip" onClick={() => app005p01PreEdit(app005p01CompanyData)}/>
              <UncontrolledTooltip placement="top" target="edittooltip">
                Edit
              </UncontrolledTooltip>

              <i className="mdi mdi-delete font-size-18 text-danger" id="deletetooltip" onClick={() => app005p01Delete(app005p01CompanyData)} />
              <UncontrolledTooltip placement="top" target="deletetooltip">
                Delete
              </UncontrolledTooltip>

          </div>
        </>
      ),
    },
  ]

  const app005p01PreAdd = () => {
    setApp005setMsg("")
    app005p02ValidInput.setFieldValue( "companyCd", "")
    app005p02ValidInput.setFieldValue( "companyNm", "")
    app005p02ValidInput.setFieldValue( "companyDesc", "")
    setApp005p01Page(false)
    setApp005p02Page(true)
  }

  const app005p01PreEdit = (app005p01CompanyData) => {
    setApp005setMsg("")
    app005p03ValidInput.setFieldValue( "companyCd", app005p01CompanyData.companyCd)
    app005p03ValidInput.setFieldValue( "companyNm", app005p01CompanyData.companyNm)
    app005p03ValidInput.setFieldValue( "companyDesc", app005p01CompanyData.companyDesc)
    setApp005p01Page(false)
    setApp005p03Page(true)
  }

  const [app005p01CompanyDelete, setApp005p01CompanyDelete] = useState(null);

  const app005p01Delete = (app005p01CompanyData) => {
    setApp005setMsg("")
    setApp005p01CompanyDelete(app005p01CompanyData);
    setApp005DeleteModal(true)
  }

  const  app005HandleDeleteMenu = () => {
    if (app005p01CompanyDelete.companyCd) {
      dispatch(deleteCompany(app005p01CompanyDelete));
      setApp005DeleteModal(false);
      setApp005p01CompanyDelete(null)
    }
  }

  const app005p04Message  = useSelector(state => {
    return state.CompanyReducer.msgDelete;
  });

  useEffect(() => {
    if(app005p04Message.status == "1"){
      dispatch(getCompanyData(app005p01TabelCompanyReq))

    }
    setApp005setMsg(app005p04Message);
  }, [app005p04Message])

//p02
const [app005p02Spinner, setApp005p02Spinner] = useState(false);

const app005p02ValidInput = useFormik({
  enableReinitialize: true,

    initialValues: {
      companyCd : '',
      companyNm : '',
      companyDesc : '',
    },
    validationSchema: Yup.object().shape({
        companyCd: Yup.string()
        .required("Wajib diisi"),
        companyNm: Yup.string()
        .required("Wajib diisi"),
        companyDesc: Yup.string()
        .required("Wajib diisi"),
    }),

  
    onSubmit: (values) => {
      setApp005p02Spinner(true);
      setApp005setMsg("")
      dispatch(saveCompany(values));
    }
  });

  const app005p02Message  = useSelector(state => {
    return state.CompanyReducer.msgAdd;
  });

  useEffect(() => {
    if(app005p02Message.status == "1"){
     
      setApp005p01Page(true); 
      setApp005p02Page(false);
      dispatch(getCompanyData(app005p01TabelCompanyReq))
    }
    setApp005setMsg(app005p02Message)
    setApp005p02Spinner(false);
  }, [app005p02Message])

//p03
const [app005p03Spinner, setapp005p03Spinner] = useState(false);
  const app005p03ValidInput = useFormik({
    enableReinitialize: true,
  
    initialValues: {
      companyCd: '',
      companyNm : '',
      companyDesc : '',
    },
    validationSchema: Yup.object().shape({
      companyCd: Yup.string()
      .required("Wajib diisi"),
      companyNm: Yup.string()
      .required("Wajib diisi"),
      companyDesc: Yup.string()
      .required("Wajib diisi"),
    }),

    
    onSubmit: (values) => {
      setapp005p03Spinner(true);
      setApp005setMsg("")
      dispatch(editCompany(values));
    }
  });

  const app005p03Message  = useSelector(state => {
    return state.CompanyReducer.msgEdit;
  });

  useEffect(() => {
    if(app005p03Message.status == "1"){
      setApp005p01Page(true); 
      setApp005p03Page(false);
      dispatch(getCompanyData(app005p01TabelCompanyReq))
    }
    setApp005setMsg(app005p03Message)
    setapp005p03Spinner(false);
  }, [app005p03Message])

//p04
  
return (
    <React.Fragment>
      
      <div className="page-content">
        <MetaTags>
          <title>
          Korindo App
          </title>
        </MetaTags>

        <DeleteModal
          show={app005deleteModal}
          onDeleteClick={app005HandleDeleteMenu}
          onCloseClick={() => setApp005DeleteModal(false)}
        />

       {app005Msg? <UncontrolledAlert toggle={app005CloseAllert} color={app005Msg.status == "1" ? "success" : "danger"}>
          {typeof app005Msg == 'string' ? app005Msg : app005Msg.listmessage?.map((msg, key) => ( <p key={key}>{"* " +msg}</p>))}</UncontrolledAlert> : null}

        <Container style={{display : app005p01Page ? 'block' : 'none'}} fluid={true}>
          <Breadcrumbs title="Forms" breadcrumbItem="Master Company" />
          
          <Row>
            <Col>
            <Card>
              <CardHeader><i className="bx bx-list-check font-size-18 align-middle me-2"></i>List Company</CardHeader>
                <CardBody>
                <React.Fragment>
                <Row className="mb-2">
                              <Col sm="2">
                                <div className="form-group m-0">
                                  <div className="input-group">
                                    <input
                                      type="text"
                                      className="form-control"
                                      placeholder="Search ..."
                                      value={app005p01TabelSearch}
                                      onChange={  e  => { setApp005p01TabelSearch(e.target.value); }} 
                                    />
                                    <div className="input-group-append">
                                      <button className="btn btn-dark" type="button" 
                                      onClick={() => {
                                        dispatch(getCompanyData(app005p01TabelCompanyReq))
                                    }}>
                                        <i className="mdi mdi-magnify" />
                                      </button>
                                    </div>
                                  </div>
                                </div>
                              </Col>
                              <Col sm="10">
                                <div className="text-sm-end">
                                <button
                                  type="button"
                                  className="btn btn-primary "
                                  onClick= {() =>  { app005p01PreAdd()}}
                                >
                                  <i className="bx bx-plus font-size-16 align-middle me-2"></i>{" "}
                                  Add
                                </button>
                                </div>
                              </Col>
                            </Row>
                            <Row>
                            <BootstrapTable
                              wrapperClasses="table-responsive"
                              keyField = "companyCd"
                              rowClasses="text-nowrap"
                              remote ={{ filter: true, pagination: true, sort: true, cellEdit: true }}
                              data = {app005p01CompanyData.data != null ? app005p01CompanyData.data.MstCompany : []}
                              columns = {app005p01TabelCompanyColumns}
                              pagination = {paginationFactory ({
                                  page : app005p01TabelCompanyReq.page,
                                  sizePerPage : app005p01TabelCompanyReq.limit,
                                  sizePerPageList : [5, 10,20],
                                  totalSize : app005p01CompanyData.data != null ? app005p01CompanyData.data.MstCompanytotal : 0,
                                  showTotal: true,
                              })}
                              classes={
                                "table align-middle table-nowrap"
                              }
                              onTableChange = {app005p01hendleTableChange}
                              striped
                              hover
                              condensed
                            />
                          </Row>
                </React.Fragment>
                </CardBody>
              </Card>
              </Col>
           </Row>
        </Container>

        {/* //p02 */}
        <Container style={{display : app005p02Page ? 'block' : 'none'}} fluid={true}>
          <Breadcrumbs title="Forms" breadcrumbItem="Add Company" />
          <Row>
            <Col lg={12}>
              <Card>
                <CardHeader><i className="bx bx-add-to-queue font-size-18 align-middle me-2"></i> Add Company</CardHeader>
                <CardBody>
                  <Form
                    onSubmit={(e) => {
                      e.preventDefault();
                      app005p02ValidInput.handleSubmit();
                      return false;
                    }}>
                    <FormGroup className="mb-0">

                      <div className="mb-3 col-sm-3">
                            <Label>Company Code</Label>
                            <Input
                              name="companyCd"
                              type="text"
                              maxLength={4}
                              onChange={app005p02ValidInput.handleChange}
                              value={app005p02ValidInput.values.companyCd || ""}
                              invalid={
                                app005p02ValidInput.touched.companyCd && app005p02ValidInput.errors.companyCd ? true : false
                              }
                            />
                            {app005p02ValidInput.touched.companyCd && app005p02ValidInput.errors.companyCd ? (
                              <FormFeedback type="invalid">{app005p02ValidInput.errors.companyCd}</FormFeedback>
                            ) : null}
                      </div>

                      <div className="mb-3 col-sm-3">
                            <Label>Company Name</Label>
                            <Input
                              name="companyNm"
                              type="text"
                              maxLength={50}
                              onChange={app005p02ValidInput.handleChange}
                              value={app005p02ValidInput.values.companyNm || ""}
                              invalid={
                                app005p02ValidInput.touched.companyNm && app005p02ValidInput.errors.companyNm ? true : false
                              }
                            />
                            {app005p02ValidInput.touched.companyNm && app005p02ValidInput.errors.companyNm ? (
                              <FormFeedback type="invalid">{app005p02ValidInput.errors.companyNm}</FormFeedback>
                            ) : null}
                      </div>

                      <div className="mb-3 col-sm-3">
                            <Label>Company Description</Label>
                            <Input
                              name="companyDesc"
                              type="text"
                              maxLength={50}
                              onChange={app005p02ValidInput.handleChange}
                              value={app005p02ValidInput.values.companyDesc || ""}
                              invalid={
                                app005p02ValidInput.touched.companyDesc && app005p02ValidInput.errors.companyDesc ? true : false
                              }
                            />
                            {app005p02ValidInput.touched.companyDesc && app005p02ValidInput.errors.companyDesc ? (
                              <FormFeedback type="invalid">{app005p02ValidInput.errors.companyDesc}</FormFeedback>
                            ) : null}
                      </div>

                      <Button type="submit" color="primary" className="ms-1">
                          <i className="bx bxs-save align-middle me-2"></i>{" "}
                            Submit
                      </Button>
                      <Spinner style={{ display: app005p02Spinner ? "block" : "none", marginTop : '-35px'}} className="ms-4" color="danger" />
                    </FormGroup>
                    
                  </Form>
                        
                </CardBody>
              </Card>
            </Col>
          </Row>
          <div className="text-sm-end">
            <button
              type="button"
              className="btn btn-danger "
              onClick ={() => { setApp005p01Page(true); setApp005p02Page(false); setApp005setMsg("")}}
              >
              <i className="bx bx-arrow-back align-middle me-2"></i>{" "}
              Back
            </button>
          </div>            
          </Container>

          {/* //p03 */}
        <Container style={{display : app005p03Page ? 'block' : 'none'}} fluid={true}>
          <Breadcrumbs title="Forms" breadcrumbItem="Edit Company" />
                              
          <Row>
            <Col lg={12}>
              <Card>
                <CardHeader><i className="bx bxs-edit-alt font-size-18 align-middle me-2"></i>Edit Company</CardHeader>
                <CardBody>
                  <Form
                    onSubmit={(e) => {
                      e.preventDefault();
                      app005p03ValidInput.handleSubmit();
                      return false;
                    }}>
                    <FormGroup className="mb-0">

                      <div className="mb-3 col-sm-3">
                            <Label>Company Code</Label>
                            <Input
                              name="companyCode"
                              type="text"
                              disabled
                              maxLength={4}
                              onChange={app005p03ValidInput.handleChange}
                              value={app005p03ValidInput.values.companyCd || ""}
                              invalid={
                                app005p03ValidInput.touched.companyCd && app005p03ValidInput.errors.companyCd ? true : false
                              }
                            />
                            {app005p03ValidInput.touched.companyCd && app005p03ValidInput.errors.companyCd ? (
                              <FormFeedback type="invalid">{app005p03ValidInput.errors.companyCd}</FormFeedback>
                            ) : null}
                      </div>

                      <div className="mb-3 col-sm-3">
                            <Label>Company Name</Label>
                            <Input
                              name="companyNm"
                              type="text"
                              maxLength={50}
                              onChange={app005p03ValidInput.handleChange}
                              value={app005p03ValidInput.values.companyNm || ""}
                              invalid={
                                app005p03ValidInput.touched.companyNm && app005p03ValidInput.errors.companyNm ? true : false
                              }
                            />
                            {app005p03ValidInput.touched.companyNm && app005p03ValidInput.errors.companyNm ? (
                              <FormFeedback type="invalid">{app005p03ValidInput.errors.companyNm}</FormFeedback>
                            ) : null}
                      </div>

                      <div className="mb-3 col-sm-3">
                            <Label>Company Description</Label>
                            <Input
                              name="companyDesc"
                              type="text"
                              maxLength={50}
                              onChange={app005p03ValidInput.handleChange}
                              value={app005p03ValidInput.values.companyDesc || ""}
                              invalid={
                                app005p03ValidInput.touched.companyDesc && app005p03ValidInput.errors.companyDesc ? true : false
                              }
                            />
                            {app005p03ValidInput.touched.companyDesc && app005p03ValidInput.errors.companyDesc ? (
                              <FormFeedback type="invalid">{app005p03ValidInput.errors.companyDesc}</FormFeedback>
                            ) : null}
                      </div>

                      <Button type="submit" color="primary" className="ms-1">
                          <i className="bx bxs-save align-middle me-2"></i>{" "}
                            Submit
                      </Button>
                      <Spinner style={{ display: app005p03Spinner ? "block" : "none", marginTop : '-35px'}} className="ms-4" color="danger" />
                    </FormGroup>
                    
                  </Form>
                        
                </CardBody>
              </Card>
            </Col>
          </Row>
          <div className="text-sm-end">
            <button
              type="button"
              className="btn btn-danger "
              onClick ={() => { setApp005p01Page(true); setApp005p03Page(false); setApp005setMsg("")}}
              >
              <i className="bx bx-arrow-back align-middle me-2"></i>{" "}
              Back
            </button>
          </div>
        </Container>
        </div>
    </React.Fragment>
    );
};

export default Company;