import {
    GET_MPLANT,
    RESP_MPLANT,
    MSGEDIT,
    MSGDELETE,
    MSGADD,
    RESET_MESSAGE,
    EDIT_PLANT,SAVE_PLANT,DELETE_PLANT

  } from "./actionTypes"

  export const geMpData = (req) => ({
    type: GET_MPLANT,
    payload: req,
  })
  
  export const respGetMp = resp => ({
    type: RESP_MPLANT,
    payload: resp,
  })

  
  export const msgEdit = resp => ({
    type: MSGEDIT,
    payload: resp,
  })

  export const msgAdd = resp => ({
    type: MSGADD,
    payload: resp,
  })

  export const msgDelete = resp => ({
    type: MSGDELETE,
    payload: resp,
  })

  export const resetMessage = (resp) => ({
    type: RESET_MESSAGE,
    payload: resp,
  })
  
  export const editPlant = (req) => ({
    type: EDIT_PLANT,
    payload: req,
  })

  export const savePlant = (req) => ({
    type: SAVE_PLANT,
    payload: req,
  })

  export const deletePlant = (req) => ({
    type: DELETE_PLANT,
    payload: req,
  })