import { call, put, takeEvery} from "redux-saga/effects"

import { GET_MPOSITION,EDIT_POSITION,SAVE_POSITION,DELETE_POSITION } from "./actionTypes"
import { respGetMp,msgEdit, msgAdd, msgDelete } from "./actions"

import { getMasterPosition,editPosition,savePosition,deletePosition } from "helpers/backend_helper"

function* fetchGetMPosition({ payload: req }) {
  try {
    const response = yield call(getMasterPosition, req)
    console.log("RESPONSE :",response);
    if(response.status == 1){
      yield put(respGetMp(response))
    }else{
      yield put(respGetMp(response))
    }
  } catch (error) {
    console.log(error);
    yield put(respGetMp({"status" : 0, "message" : "Error Get Data"}))
  }
}

function* fetchEditPosition({ payload: req }) {
  try {
    const response = yield call(editPosition, req)
    yield put(msgEdit(response))
  } catch (error) {
    console.log(error);
    yield put(msgEdit({"status" : 0, "message" : "Error Get Data"}))
  }
}

function* fetchSavePosition({ payload: req }) {
  try {
    const response = yield call(savePosition, req)
    yield put(msgAdd(response))
  } catch (error) {
    console.log(error);
    yield put(msgAdd({"status" : 0, "message" : "Error Get Data"}))
  }
}

function* fetchDeletePosition({ payload: req }) {
  try {
    const response = yield call(deletePosition, req)
    yield put(msgDelete(response))
  } catch (error) {
    console.log(error);
    yield put(msgDelete({"status" : 0, "data" : "Error Delete Data"}))
  }
}

function* mstPositionSaga() {
    
  yield takeEvery(GET_MPOSITION, fetchGetMPosition)
  yield takeEvery(EDIT_POSITION, fetchEditPosition)
  yield takeEvery(SAVE_POSITION, fetchSavePosition)
  yield takeEvery(DELETE_POSITION, fetchDeletePosition)

}

export default mstPositionSaga
