import {
    MSGEDIT,
    RESET_MESSAGE,
    EDIT_USER
  } from "./actionTypes"

  export const msgEdit = resp => ({
    type: MSGEDIT,
    payload: resp,
  })

  export const resetMessage = (resp) => ({
    type: RESET_MESSAGE,
    payload: resp,
  })
  
  export const editUser = (req) => ({
    type: EDIT_USER,
    payload: req,
  })
