import { call, put, takeEvery} from "redux-saga/effects"

import {

        GET_DIVISI,
        EDIT_DIVISI,
        SAVE_DIVISI,
        DELETE_DIVISI,

} from "./actionTypes"

import { respGetDivisi, msgAdd, msgEdit, msgDelete } from "./actions"

import { getDivisi,editDivisi,saveDivisi,deleteDivisi } from "helpers/backend_helper"

function* fetchgetDivisi ({ payload: req }) {
    try {
      const response = yield call(getDivisi, req)
      if(response.status == 1){
        yield put(respGetDivisi(response))
      }else{
        yield put(respGetDivisi(response))
      }
    } catch (error) {
      console.log(error);
      yield put(respGetDivisi({"status" : 0, "message" : "Error Get Data"}))
    }
  }

  function* fetchEditDivisi ({ payload: req }) {
    try {
      const response = yield call(editDivisi, req)
      yield put(msgEdit(response))
    } catch (error) {
      console.log(error);
      yield put(msgEdit({"status" : 0, "message" : "Error Edit Data"}))
    }
  }

  function* fetchSaveDivisi ({ payload: req }) {
    try {
      const response = yield call(saveDivisi, req)
      yield put(msgAdd(response))
    } catch (error) {
      yield put(msgAdd({"status" : 0, "message" : "Error Save Data"}))
    }
  }

  function* fetchDeleteDivisi ({ payload: req }) {
    try {
      const response = yield call(deleteDivisi, req)
      yield put(msgDelete(response))
    } catch (error) {
      console.log(error);
      yield put(msgDelete({"status" : 0, "message" : "Error Delete Data"}))
    }
  }

  function* DivisiSaga() {
    
    yield takeEvery(GET_DIVISI, fetchgetDivisi)
    yield takeEvery(EDIT_DIVISI, fetchEditDivisi)
    yield takeEvery(SAVE_DIVISI, fetchSaveDivisi)
    yield takeEvery(DELETE_DIVISI, fetchDeleteDivisi)
  
  }

  export default DivisiSaga