import {
    GET_MPOSITION,
    RESP_MPOSITION,
    MSGEDIT,
    MSGDELETE,
    MSGADD,
    RESET_MESSAGE,
    EDIT_POSITION,
    SAVE_POSITION,
    DELETE_POSITION,
  } from "./actionTypes"

  const INIT_STATE = {
    respGetmPosition: {},
    msgEdit: "",
    msgAdd: "",
    msgDelete: "",
  }

  const mstPositionReducer = (state = INIT_STATE, action) => {
    
    switch (action.type) {
      case GET_MPOSITION:
        return {
          ...state,
        }
      case RESP_MPOSITION:
        return {
          ...state,
          respGetmPosition: action.payload,
        }
      case MSGEDIT:
        return {
          ...state,
          msgEdit: action.payload,
        }
      case MSGDELETE:
          return {
            ...state,
            msgDelete: action.payload,
      }
      case MSGADD:
        return {
          ...state,
          msgAdd: action.payload,
      }
      case RESET_MESSAGE :
          return {
             ...state,
             msgEdit: "",
             msgAdd: "",
             msgDelete: "",
          }
      case EDIT_POSITION:
        return {
          ...state,
        }
      case SAVE_POSITION:
        return {
          ...state,
      }
      case DELETE_POSITION:
        return {
          ...state,
      }
      default:
        return state
    }
  }
  
  export default mstPositionReducer