import {
    GET_SETTING,
    RESP_SETTING,
    MSGEDIT,
    MSGADD,
    RESET_MESSAGE,
    EDIT_SETTING,
    SAVE_SETTING,
    
  } from "./actionTypes"


  export const getSetting = (req) => ({
    type: GET_SETTING,
    payload: req,
  })

  export const respSettings = resp => ({
    type: RESP_SETTING,
    payload: resp,
  })

  export const msgEdit = resp => ({
    type: MSGEDIT,
    payload: resp,
  })

  export const msgAdd = resp => ({
    type: MSGADD,
    payload: resp,
  })

  export const resetMessage = (resp) => ({
    type: RESET_MESSAGE,
    payload: resp,
  })

  export const editSetting = (req) => ({
    type: EDIT_SETTING,
    payload: req,
  })

  export const saveSetting = (req) => ({
    type: SAVE_SETTING,
    payload: req,
  })