import {
    MSGEDIT,
    EDIT_USER,
    RESET_MESSAGE
  } from "./actionTypes"

  const INIT_STATE = {
    msgEdit: "",
  }

  const userReducer = (state = INIT_STATE, action) => {
    
    switch (action.type) {
      case MSGEDIT:
        return {
          ...state,
          msgEdit: action.payload,
        }
      case RESET_MESSAGE :
          return {
             ...state,
             msgEdit: "",
          }
      case EDIT_USER:
        return {
          ...state,
        }
      default:
        return state
    }
  }
  
  export default userReducer