import { call, put, takeEvery} from "redux-saga/effects"

import { GET_SETTING,EDIT_SETTING,SAVE_SETTING} from "./actionTypes"
import { msgEdit, msgAdd, respSettings } from "./actions"
import { getSetting,editSetting,saveSetting } from "helpers/backend_helper"

function* fetchGetSetting({ payload: req }) {
    try {
      const response = yield call(getSetting, req)
      console.log("RESPONSE :",response);
      if(response.status == 1){
        yield put(respSettings(response))
      }else{
        yield put(respSettings(response))
      }
    } catch (error) {
      console.log(error);
      yield put(respSettings({"status" : 0, "message" : "Error Get Data"}))
    }
  }
function* fetchEditSetting({ payload: req }) {
    try {
      const response = yield call(editSetting, req)
      yield put(msgEdit(response))
    } catch (error) {
      console.log(error);
      yield put(msgEdit({"status" : 0, "message" : "Error Get Data"}))
    }
  }
  
  function* fetchSaveSetting({ payload: req }) {
    try {
      const response = yield call(saveSetting, req)
      yield put(msgAdd(response))
    } catch (error) {
      console.log(error);
      yield put(msgAdd({"status" : 0, "message" : "Error Get Data"}))
    }
  }

  function* mstDtlsettings() {
    
    yield takeEvery(GET_SETTING, fetchGetSetting)
    yield takeEvery(EDIT_SETTING, fetchEditSetting)
    yield takeEvery(SAVE_SETTING, fetchSaveSetting)
 
  
  }
  
  export default mstDtlsettings