import {
        GET_DIVISI,
        RESP_DIVISI,
        EDIT_DIVISI,
        SAVE_DIVISI,
        DELETE_DIVISI,
        RESET_MESSAGE,
        MSGADD,
        MSGEDIT,
        MSGDELETE,
} from "./actionTypes";

export const getDivisiData = (req) => ({
    type: GET_DIVISI,
    payload: req,
  })
  
  export const respGetDivisi = resp => ({
    type: RESP_DIVISI,
    payload: resp,
  })

  export const editDivisi = (req) => ({
    type: EDIT_DIVISI,
    payload: req,
  })

  export const saveDivisi = (req) => ({
    type: SAVE_DIVISI,
    payload: req,
  })

  export const deleteDivisi = (req) => ({
    type: DELETE_DIVISI,
    payload: req,
  })

  export const msgEdit = resp => ({
    type: MSGEDIT,
    payload: resp,
  })

  export const msgAdd = resp => ({
    type: MSGADD,
    payload: resp,
  })

  export const msgDelete = resp => ({
    type: MSGDELETE,
    payload: resp,
  })

  export const resetMessage = (resp) => ({
    type: RESET_MESSAGE,
    payload: resp,
  })

  