import Alert from 'react-s-alert';

const AlertCustom = (status, message) => {

  if(typeof message === 'object'){
    let msgArray = '';
      const listItems = message.map(
        (message) => '<li>' + message.desc + '</li>'
        )
        msgArray = '<ul class="errorList">'
        for (var i = 0; i < listItems.length; i++) {
          msgArray = msgArray + ` ${listItems[i]}`
        }
        msgArray = msgArray + ` </ul>`

      console.log(msgArray);
      if(status == 1){
        Alert.success(msgArray, {
          position: 'top-right',
          effect: 'slide',
          html: true,
          beep: false,
          timeout: 'none',
          offset: 50
        });
      }else{
        Alert.error(msgArray, {
          position: 'top-right',
          effect: 'slide',
          html: true,
          beep: false,
          timeout: 'none',
          offset: 50
        });
      }
      
  }else{
    console.log('string');
    if(status == 1){
      
      Alert.error(message, {
        position: 'top-right',
        effect: 'slide',
        html: true,
        beep: false,
        timeout: 'none',
        offset: 50
      });
  
    }else{
  
      Alert.success(message, {
        position: 'top-right',
        effect: 'slide',
        html: true,
        beep: false,
        timeout: 'none',
        offset: 50
      });
  
    }
  }

  
}
export default AlertCustom;
