import React, { useState, useEffect , useCallback } from "react";
import MetaTags from "react-meta-tags";  

import BootstrapTable from "react-bootstrap-table-next";
import paginationFactory from "react-bootstrap-table2-paginator";

import Breadcrumbs from "../../components/Common/Breadcrumb";
import '../../config';
import {
  Row,
  Col,
  Card,
  CardBody,
  Container,
  UncontrolledTooltip,
  CardHeader,
} from "reactstrap";


import { getteamData,getteamChildData } from "../../store/app013/actions"
import { useSelector, useDispatch } from "react-redux"

const Team = () => {

  const dispatch = useDispatch();
  const [app013p01firstRenderDone, setApp013p01firstRenderDone] = useState(false);
  const [app013p01Page, setApp013p01Page] = useState(true)
  const [app013p02Page, setApp013p02Page] = useState(false)
  const [app013p01TeamCd, setApp013p01TeamCd] = useState('');
  const [app013p01DivisiId, setApp013p01DivisiId] = useState('');
  
  //const [appDetailPage, setAppDetailPage] = useState(false)

  //p01
  const [app013p01TabelSearch, setApp013p01TabelSearch] = useState('');
  
  const [app013p01TabelTeamReq, setApp013p01TabelTeamReq] = useState({page : 1, limit: 10, offset: 0, sort: "teamCd", order: "asc", search: {any : app013p01TabelSearch}});

  const app013p01hendleTableChange = (type, {page, sortField, sortOrder, sizePerPage}) => {   
    if(type === "sort"){
      setApp013p01TabelTeamReq({page : 1, limit: sizePerPage, offset: 0, sort: sortField, order: sortOrder, search: {any : app013p01TabelSearch}});
    }
    if(type==="pagination"){
      setApp013p01TabelTeamReq({page : page, limit: sizePerPage, offset: ((page-1) * sizePerPage), sort: sortField, order: sortOrder, search: {any : app013p01TabelSearch}});
    }
  };

  useEffect(() => {
    setApp013p01firstRenderDone(true);
  }, [])

  useEffect(() => {
    setApp013p01TabelTeamReq({page : 1, limit: 10, offset: 0, sort: "teamCd", order: "asc", search: {any : app013p01TabelSearch}});
  }, [app013p01TabelSearch])
  

  const app013p01TeamData  = useSelector(state => {
    return state.TeamReducer.respGetteam;
  });

  // useEffect(() => {
  //   if(app013p01TeamData.status == "0"){
  //     setApp013setMsg(app013p01TeamData)
  //   }
  // }, [app013p01TeamData])
  
  
  useEffect(() => {
    if(app013p01firstRenderDone){
      dispatch(getteamData(app013p01TabelTeamReq))
    }
  }, [app013p01TabelTeamReq])

  const app013p01TabelTeamColumns = [
    {
      dataField: "no",
      text: "No",
      hidden: true,
      headerStyle: {textAlign: 'center'},
    },
    {
      dataField: "teamCd",
      text: "Team Code",
      sort: true,
      headerStyle: {textAlign: 'center'},
    },
    {
      dataField: "teamNm",
      text: "Team Name",
      sort: true,
      headerStyle: {textAlign: 'center'},
    },
    {
      dataField: "divisiId",
      text: "Divisi Id",
      sort: true,
      hidden:true,
      headerStyle: {textAlign: 'center'},
    },
    {
      dataField: "divisiNm",
      text: "Divisi Name",
      sort: true,
      headerStyle: {textAlign: 'center'},
    },
    {
      dataField: "plantCd",
      text: "Plant Code",
      hidden:true,
      sort: true,
      headerStyle: {textAlign: 'center'},
    },
    {
      dataField: "plantNm",
      text: "Plant Name",
      sort: true,
      headerStyle: {textAlign: 'center'},
    },
    {
      dataField: "userIdKetua",
      text: "Tenaga Kerja ID Ketua",
      sort: true,
      headerStyle: {textAlign: 'center'},
    },
    {
      dataField: "userNmKetua",
      text: "Ketua Tim Name",
      sort: true,
      headerStyle: {textAlign: 'center'},
    },
    {
      dataField: "action",
      isDummyField: true,
      text: "Action",
      headerStyle: {textAlign: 'center'},
      formatter: (cellContent, app013p01TeamData) => (
        <>
          <div style={{justifyContent : 'center'}} className="d-flex gap-3">
              {/* <i className="mdi mdi-pencil font-size-18  text-primary" id="edittooltip" onClick={() => app003p01PreEdit(app003p01RoleData)}/>
              <UncontrolledTooltip placement="top" target="edittooltip">
                Edit
              </UncontrolledTooltip> */}

              <i className="mdi mdi-file-check-outline font-size-18 text-info" id="detailtooltip" onClick={() => app013p01PreDetail(app013p01TeamData)} />
              <UncontrolledTooltip placement="top" target="detailtooltip">
                Detail
              </UncontrolledTooltip>
            
              {/* <i className="mdi mdi-delete font-size-18 text-danger" id="deletetooltip" onClick={() => app003p01Delete(app003p01RoleData)} />
              <UncontrolledTooltip placement="top" target="deletetooltip">
                Delete
              </UncontrolledTooltip> */}

          </div>
        </>
      ),
    },
  ]

  const app013p01PreDetail = (app013p01TeamData) => {
    setApp013p01Page(false)
    setApp013p02Page(true)
    setApp013p01TeamCd(app013p01TeamData.teamCd)
    setApp013p01DivisiId(app013p01TeamData.divisiId)
    //setAppDetailPage(true)
  }

  //p02

  const [app013p02TabelSearch, setApp013p02TabelSearch] = useState('');
  const [app013p02TabelTeamReq, setApp013p02TabelTeamReq] = useState({ page: 1, limit: 10, offset: 0, sort: "teamCd", order: "asc", search: { any: app013p02TabelSearch, "teamCd": app013p01TeamCd, "divId" : app013p01DivisiId} });

  const app013p02hendleTableChange = (type, { page, sortField, sortOrder, sizePerPage }) => {
    if (type === "sort") {
      setApp013p02TabelTeamReq({ page: 1, limit: sizePerPage, offset: 0, sort: sortField, order: sortOrder, search: { any: app013p02TabelSearch, "teamCd": app013p01TeamCd, "divId" : app013p01DivisiId } });
    }
    if (type === "pagination") {
      setApp013p02TabelTeamReq({ page: page, limit: sizePerPage, offset: ((page - 1) * sizePerPage), sort: sortField, order: sortOrder, search: { any: app013p02TabelSearch, "teamCd": app013p01TeamCd, "divId" : app013p01DivisiId } });
    }
  };

  useEffect(() => {
    setApp013p02TabelTeamReq({ page: 1, limit: 10, offset: 0, sort: "teamCd", order: "asc", search: { any: app013p02TabelSearch, "teamCd": app013p01TeamCd, "divId" : app013p01DivisiId } });
  }, [app013p01TeamCd, app013p01DivisiId])

  const app013p02TeamData = useSelector(state => {
    //console.log(state)
    return state.TeamReducer.respGetteamChild;
  });

  useEffect(() => {
    setApp013p02TabelTeamReq({ page: 1, limit: 10, offset: 0, sort: "teamCd", order: "asc", search: { any: app013p02TabelSearch, "teamCd": app013p01TeamCd, "divId" : app013p01DivisiId } });
  }, [app013p02TabelSearch])

  useEffect(() => {
    if (app013p01firstRenderDone) {
      dispatch(getteamChildData(app013p02TabelTeamReq))
    }
  }, [app013p02TabelTeamReq])

  const app013p02TabelTeamColumns = [
    {
      dataField: "tkId",
      text: "Tenaga Kerja ID",
      sort: true,
      headerStyle: { textAlign: 'center' },
    },
    {
      dataField: "tkNm",
      text: "Tenaga Kerja Nm",
      sort: true,
      headerStyle: { textAlign: 'center' },
    },
    {
      dataField: "companyNm",
      text: "Company Name",
      sort: true,
      headerStyle: { textAlign: 'center' },
    },
    {
      dataField: "plantNm",
      text: "Plant Name",
      sort: true,
      headerStyle: { textAlign: 'center' },
    },
    {
      dataField: "divisiNm",
      text: "Divisi Name",
      sort: true,
      headerStyle: { textAlign: 'center' },
    },
    // {
    //   dataField: "action",
    //   isDummyField: true,
    //   text: "Action",
    //   headerStyle: { textAlign: 'center' },
    //   formatter: (cellContent, app013p02TeamData) => (
    //     <>
    //       {/* <div style={{ justifyContent: 'center' }} className="d-flex gap-3">
    //         <i className="mdi mdi-delete font-size-18 text-danger" id="deletetooltip" onClick={() => app003p04Delete(app003p04RoleMenuData)} />
    //         <UncontrolledTooltip placement="top" target="deletetooltip">
    //           Delete
    //         </UncontrolledTooltip>

    //       </div> */}
    //     </>
    //   ),
    // },
  ]

  return (
    <React.Fragment>
      
      <div className="page-content">
        <MetaTags>
          <title>
          Korindo App
          </title>
        </MetaTags>

        <Container style={{display : app013p01Page ? 'block' : 'none'}} fluid={true}>
          <Breadcrumbs title="Forms" breadcrumbItem="Team" />
          
          <Row>
            <Col>
              <Card>
              <CardHeader><i className="bx bx-list-check font-size-18 align-middle me-2"></i>List Team</CardHeader>
                <CardBody>
                            <Row className="mb-2">
                              <Col sm="2">
                                <div className="form-group m-0">
                                  <div className="input-group">
                                    <input
                                      type="text"
                                      className="form-control"
                                      placeholder="Search ..."
                                      value={app013p01TabelSearch}
                                      onChange={ ( e ) => setApp013p01TabelSearch(e.target.value) } 
                                    />
                                    <div className="input-group-append">
                                      <button className="btn btn-dark" type="button" 
                                      onClick={() => {
                                        dispatch(getteamData(app013p01TabelTeamReq))
                                    }}>
                                        <i className="mdi mdi-magnify" />
                                      </button>
                                    </div>
                                  </div>
                                </div>
                              </Col>
                            </Row>
                          <Row>
                            <BootstrapTable
                              wrapperClasses="table-responsive"
                              keyField = "no"
                              rowClasses="text-nowrap"
                              remote ={{ filter: true, pagination: true, sort: true, cellEdit: true }}
                              data = {app013p01TeamData.data != null ? app013p01TeamData.data.team : []}
                              columns = {app013p01TabelTeamColumns}
                              pagination = {paginationFactory ({
                                  page : app013p01TabelTeamReq.page,
                                  sizePerPage : app013p01TabelTeamReq.limit,
                                  sizePerPageList : [5, 10,20],
                                  totalSize : app013p01TeamData.data != null ? app013p01TeamData.data.teamtotal : 0,
                                  showTotal: true,
                              })}
                              classes={
                                "table align-middle table-nowrap"
                              }
                              onTableChange = {app013p01hendleTableChange}
                              striped
                              hover
                              condensed
                            />
                          </Row>
                </CardBody>
              </Card>
            </Col>
          </Row>
         
        </Container>

        {/* //p02 */}
        <Container style={{display : app013p02Page ? 'block' : 'none'}} fluid={true}>
          <Breadcrumbs title="Forms" breadcrumbItem="Team" />
                              
          <Row>
            <Col>
              <Card>
              <CardHeader><i className="bx bx-list-check font-size-18 align-middle me-2"></i>List Tenaga Kerja</CardHeader>
                <CardBody>
                <Row className="mb-2">
                              <Col sm="2">
                                <div className="form-group m-0">
                                  <div className="input-group">
                                    <input
                                      type="text"
                                      className="form-control"
                                      placeholder="Search ..."
                                      value={app013p02TabelSearch}
                                      onChange={ ( e ) => setApp013p02TabelSearch(e.target.value) } 
                                    />
                                    <div className="input-group-append">
                                      <button className="btn btn-dark" type="button" 
                                      onClick={() => {
                                        setApp013p02TabelTeamReq({ page: 1, limit: 10, offset: 0, sort: "teamCd", order: "asc", search: { any: app013p02TabelSearch, "teamCd": app013p01TeamCd, "divId" : app013p01DivisiId } });
                                       // dispatch(getteamChildData(app013p02TabelTeamReq))
                                    }}>
                                        <i className="mdi mdi-magnify" />
                                      </button>
                                    </div>
                                  </div>
                                </div>
                              </Col>
                            </Row>
                          <Row>
                            <BootstrapTable
                              wrapperClasses="table-responsive"
                              keyField = "tkId"
                              rowClasses="text-nowrap"
                              remote ={{ filter: true, pagination: true, sort: true, cellEdit: true }}
                              data = {app013p02TeamData.data != null ? app013p02TeamData.data.tchild : []}
                              columns = {app013p02TabelTeamColumns}
                              pagination = {paginationFactory ({
                                  page : app013p02TabelTeamReq.page,
                                  sizePerPage : app013p02TabelTeamReq.limit,
                                  sizePerPageList : [5, 10,20],
                                  totalSize : app013p02TeamData.data != null ? app013p02TeamData.data.tchildtotal : 0,
                                  showTotal: true,
                              })}
                              classes={
                                "table align-middle table-nowrap"
                              }
                              onTableChange = {app013p02hendleTableChange}
                              striped
                              hover
                              condensed
                            />
                          </Row>
                </CardBody>
              </Card>
            </Col>
          </Row>

          <div className="text-sm-end">
            <button
              type="button"
              className="btn btn-danger "
              onClick ={() => { setApp013p01Page(true); setApp013p02Page(false); }}
              >
              <i className="bx bx-arrow-back align-middle me-2"></i>{" "}
              Back
            </button>
          </div>
        </Container>

    </div>

        

    </React.Fragment>
  );
};
  
export default Team
