import {
    GET_SETTING,
    RESP_SETTING,
    MSGEDIT,
    MSGADD,
    RESET_MESSAGE,
    EDIT_SETTING,
    SAVE_SETTING,
      } from "./actionTypes"

  const INIT_STATE = {
    respSettings: {},
    msgEdit: "",
    msgAdd: "",
      }


    const mstDtlSettingReducer = (state = INIT_STATE, action) => {

  switch (action.type) {
    case GET_SETTING:
      return {
        ...state
      }
    case RESP_SETTING:
      return {
        ...state,
        respSettings: action.payload
      }
    case MSGEDIT:
      return {
        ...state,
        msgEdit: action.payload
      }

    case MSGADD:
      return {
        ...state,
        msgAdd: action.payload
      }
    case RESET_MESSAGE:
      return {
        ...state,
        msgEdit: "",
        msgAdd: ""
      }
    case EDIT_SETTING:
      return {
        ...state
      }
    case SAVE_SETTING:
      return {
        ...state
      }

    default:
      return state
  }

}

      export default mstDtlSettingReducer;