import React, { useState, useEffect , useCallback  } from "react"
import MetaTags from 'react-meta-tags';
import Breadcrumbs from "../../components/Common/Breadcrumb";
import { useFormik, } from "formik";
import * as Yup from "yup";
import '../../config';
import {
  Row,
  Col,
  Card,
  CardBody,
  Container,
  Button,
  Label,
  Input,
  FormFeedback,
  Form,
  Spinner,
  FormGroup,
  CardHeader,
  UncontrolledAlert,
} from "reactstrap"

import { editUser, resetMessage } from "../../store/app007/actions"
import { useSelector, useDispatch } from "react-redux"

import Lov from "../../common/Lov";
import { getDiv, getPosition } from "../../store/lov/actions"
import { getCombo } from "../../store/combo/actions"

const ChangeUser = () => {

  const dispatch = useDispatch();
  const [app007p01Page, setApp007p01Page] = useState(true)
  const [app007Msg, setApp007setMsg] = useState("")

  useEffect(() => {
    dispatch(resetMessage());
    dispatch(getCombo({"name":"combo-akse-apps"}))
  }, [dispatch])

  const app007CloseAllert = () => {
    setApp007setMsg("")
  }

const [app007p01Spinner, setApp007p01Spinner] = useState(false);
  const u = JSON.parse(localStorage.getItem("user"))
  
  const app007p01ValidInput = useFormik({
    enableReinitialize: true,
      
      initialValues: {
        userId: u != null ? u.userId : '',
        userFisrtNm :u != null ? u.userFisrtNm : '',
        userLastNm: u != null ? u.userLastNm : '',
        password : '',
        rePassword : '',
        divisiNm : u != null ? u.divisiNm : '',
        divisiId : u != null ? u.divisiId : null,
        positionNm :  u != null ? u.positionNm : '',
        positionId : u != null ? u.positionId : null,
        aksesApps : u != null ? u.aksesApps.toString() : null,
      },
      
      validationSchema: Yup.object().shape({
        userId: Yup.string()
          .required("Wajib diisi"),
        userFisrtNm: Yup.string()
          .required("Wajib diisi"),
        userLastNm: Yup.string()
          .required("Wajib diisi"),
        divisiNm: Yup.string()
          .required("Wajib diisi"),
        positionNm: Yup.string()
          .required("Wajib diisi"),
        aksesApps: Yup.string()
          .required("Wajib diisi"),
      }),
  
      onSubmit: (values) => {
        setApp007p01Spinner(true);
        setApp007setMsg("")
        dispatch(editUser(values));
      }
    });

    const app007p01Message  = useSelector(state => {
      return state.UserReducer.msgEdit;
    });


  useEffect(() => {
    if(app007p01Message.status == "1"){
      setApp007p01Page(true); 
      const u = JSON.parse(localStorage.getItem("user"))
      u.aksesApps =  app007p01ValidInput.values.aksesApps
      u.divisiId = app007p01ValidInput.values.divisiId
      u.divisiNm = app007p01ValidInput.values.divisiNm
      u.positionId = app007p01ValidInput.values.positionId
      u.positionNm = app007p01ValidInput.values.positionNm
      u.userFisrtNm = app007p01ValidInput.values.userFisrtNm
      u.userLastNm = app007p01ValidInput.values.userLastNm
      localStorage.setItem("user", JSON.stringify(u));
    }
    setApp007setMsg(app007p01Message)
    setApp007p01Spinner(false);
  }, [app007p01Message])

  const [app007p01SearchLovDivisi, setApp007p01SearchLovDivisi] = useState(u != null ? u.divisiNm : '')


  const app007p01LovDivisiColumns = [
    {
      dataField: "divisiId",
      text: "Divisi ID",
      sort: true,
      headerStyle: { textAlign: 'center' },
    },
    {
      dataField: "divisiCd",
      text: "Divisi Code",
      sort: true,
      headerStyle: { textAlign: 'center' },
    },
    {
      dataField: "divisiNm",
      text: "Divisi Name",
      sort: true,
      headerStyle: { textAlign: 'center' },
    },
    {
      dataField: "plantNm",
      text: "Plant Name",
      sort: true,
      headerStyle: { textAlign: 'center' },
    },
  ]

  function app007p01callBackLovDivisi(row) {
    app007p01ValidInput.setFieldValue( "divisiId", row.divisiId)
  }

  const [app007p01SearchLovPosition, setApp007p01SearchLovPosition] = useState(u != null ? u.positionNm : '')

  const app007p01LovPositionColumns = [
    {
      dataField: "positionId",
      text: "Position ID",
      sort: true,
      headerStyle: { textAlign: 'center' },
    },
    {
      dataField: "positionNm",
      text: "Position Name",
      sort: true,
      headerStyle: { textAlign: 'center' },
    },
  ]

  function app007p01callBackLovPosition(row) {
    app007p01ValidInput.setFieldValue( "positionId", row.positionId)
  }
  
  const {
    app007SelectedAksesApp
  } = useSelector(state => ({
    app007SelectedAksesApp: state.ComboReducer.respRoleAksesType.data != null ? state.ComboReducer.respRoleAksesType.data : []
  }));

    return (
      <React.Fragment>
        <div className="page-content">
          <MetaTags>
            <title>Korindo App</title>
          </MetaTags>

        {app007Msg !== ""? <UncontrolledAlert toggle={app007CloseAllert} color={app007Msg.status == "1" ? "success" : "danger"}>
          {typeof app007Msg == 'string' ? app007Msg : app007Msg.listmessage?.map((msg, key) => ( <p key={key}>{"* " +msg}</p>))}</UncontrolledAlert> : null}

          <Container style={{display : app007p01Page ? 'block' : 'none'}} fluid={true}>
          <Breadcrumbs title="Forms" breadcrumbItem="Change User" />
                              
          <Row>
            <Col lg={12}>
              <Card>
                <CardHeader><i className="bx bxs-edit-alt font-size-18 align-middle me-2"></i>Edit User</CardHeader>
                <CardBody>
                  <Form
                    onSubmit={(e) => {
                      e.preventDefault();
                      app007p01ValidInput.handleSubmit();
                      return false;
                    }}>
                    <FormGroup className="mb-0">
                      <Row>
                      <Col md="5">
                          <div className="mb-3 col-sm-10">
                                <Label>User ID</Label>
                                <Input
                                  name="userId"
                                  type="text"
                                  disabled
                                  maxLength={50}
                                  onChange={app007p01ValidInput.handleChange}
                                  value={app007p01ValidInput.values.userId || ""}
                                  invalid={
                                    app007p01ValidInput.touched.userId && app007p01ValidInput.errors.userId ? true : false
                                  }
                                />
                                {app007p01ValidInput.touched.userId && app007p01ValidInput.errors.userId ? (
                                  <FormFeedback type="invalid">{app007p01ValidInput.errors.userId}</FormFeedback>
                                ) : null}
                          </div>

                          <div className="mb-3 col-sm-10">
                                <Label>First Name</Label>
                                <Input
                                  name="userFisrtNm"
                                  type="text"
                                  maxLength={50}
                                  onChange={app007p01ValidInput.handleChange}
                                  value={app007p01ValidInput.values.userFisrtNm || ""}
                                  invalid={
                                    app007p01ValidInput.touched.userFisrtNm && app007p01ValidInput.errors.userFisrtNm ? true : false
                                  }
                                />
                                {app007p01ValidInput.touched.userFisrtNm && app007p01ValidInput.errors.userFisrtNm ? (
                                  <FormFeedback type="invalid">{app007p01ValidInput.errors.userFisrtNm}</FormFeedback>
                                ) : null}
                          </div>
                          
                          <div className="mb-3 col-sm-10">
                                <Label>Last Name</Label>
                                <Input
                                  name="userLastNm"
                                  type="text"
                                  maxLength={50}
                                  onChange={app007p01ValidInput.handleChange}
                                  value={app007p01ValidInput.values.userLastNm || ""}
                                  invalid={
                                    app007p01ValidInput.touched.userLastNm && app007p01ValidInput.errors.userLastNm ? true : false
                                  }
                                />
                                {app007p01ValidInput.touched.userLastNm && app007p01ValidInput.errors.userLastNm ? (
                                  <FormFeedback type="invalid">{app007p01ValidInput.errors.userLastNm}</FormFeedback>
                                ) : null}
                          </div>

                          <Lov title="Divisi"
                              keyFieldData="divisiId"
                              disabled={true}
                              LenghtLabel=""
                              LenghtDiv="mb-3 col-sm-10"
                              columns={app007p01LovDivisiColumns}
                              getData={getDiv}
                              pageSize={10}
                              callbackFunc = {app007p01callBackLovDivisi}
                              // onChangeFunc = {null}
                              defaultSetInput="divisiNm"
                              invalidData={app007p01ValidInput}
                              fieldValue="divisiNm"
                              stateSearchInput={app007p01SearchLovDivisi}
                              stateSearchInputSet={setApp007p01SearchLovDivisi}
                              touchedLovField={app007p01ValidInput.touched.divisiNm}
                              errorLovField={app007p01ValidInput.errors.divisiNm}
                            />

                            <Lov title="Position"
                              keyFieldData="positionId"
                              disabled={true}
                              LenghtLabel=""
                              LenghtDiv="mb-3 col-sm-10"
                              columns={app007p01LovPositionColumns}
                              getData={getPosition}
                              pageSize={10}
                              callbackFunc = {app007p01callBackLovPosition}
                              // onChangeFunc = {null}
                              defaultSetInput="positionNm"
                              invalidData={app007p01ValidInput}
                              fieldValue="positionNm"
                              stateSearchInput={app007p01SearchLovPosition}
                              stateSearchInputSet={setApp007p01SearchLovPosition}
                              touchedLovField={app007p01ValidInput.touched.positionNm}
                              errorLovField={app007p01ValidInput.errors.positionNm}
                            />

                            <div className="mb-3 col-sm-10">
                              <Label>Akses App</Label>
                              <Input
                                      disabled
                                      type="select"
                                      name="aksesApps"
                                      onChange= {app007p01ValidInput.handleChange}
                                      onBlur={app007p01ValidInput.handleBlur}
                                      value={app007p01ValidInput.values.aksesApps || ""}
                                      invalid={
                                        app007p01ValidInput.touched.aksesApps && app007p01ValidInput.errors.aksesApps ? true : false
                                      }
                                    >
                                      <option></option>
                                      {
                                        app007SelectedAksesApp.dtlsetting?.map((value, key) =>
                                        <option key={key} value={value.id}>{value.desc}</option>) 
                                      }
                                    </Input>
                                    {app007p01ValidInput.touched.aksesApps && app007p01ValidInput.errors.aksesApps ? (
                                      <FormFeedback type="invalid">{app007p01ValidInput.errors.aksesApps}</FormFeedback>
                                    ) : null}
                          </div>
                        </Col>
                        <Col md="5">
                          <div className="mb-3 col-sm-8">
                                <Label>New Password</Label>
                                <Input
                                  name="password"
                                  type="password"
                                  maxLength={50}
                                  onChange={app007p01ValidInput.handleChange}
                                  value={app007p01ValidInput.values.password || ""}
                                  invalid={
                                    app007p01ValidInput.touched.password && app007p01ValidInput.errors.password ? true : false
                                  }
                                />
                                {app007p01ValidInput.touched.password && app007p01ValidInput.errors.password ? (
                                  <FormFeedback type="invalid">{app007p01ValidInput.errors.password}</FormFeedback>
                                ) : null}
                          </div>
                          
                          <div className="mb-3 col-sm-8">
                                <Label>Repeat Password</Label>
                                <Input
                                  name="rePassword"
                                  type="password"
                                  maxLength={50}
                                  onChange={app007p01ValidInput.handleChange}
                                  value={app007p01ValidInput.values.rePassword || ""}
                                  invalid={
                                    app007p01ValidInput.touched.rePassword && app007p01ValidInput.errors.rePassword ? true : false
                                  }
                                />
                                {app007p01ValidInput.touched.rePassword && app007p01ValidInput.errors.rePassword ? (
                                  <FormFeedback type="invalid">{app007p01ValidInput.errors.rePassword}</FormFeedback>
                                ) : null}
                          </div>
                          <span style={{color: "red"}}> Input ulang jika ingin merubah password !!!</span>
                        </Col>
                      </Row>
                      <Button type="submit" color="primary" className="ms-1">
                          <i className="bx bxs-save align-middle me-2"></i>{" "}
                            Submit
                      </Button>
                      <Spinner style={{ display: app007p01Spinner ? "block" : "none", marginTop : '-35px'}} className="ms-4" color="danger" />
                    </FormGroup>
                    
                  </Form>
                        
                </CardBody>
              </Card>
            </Col>
          </Row>
        </Container>

        </div>
      </React.Fragment>
  );
};
  
export default ChangeUser
