import { call, put, takeEvery, all} from "redux-saga/effects"

// Crypto Redux StatesgetMenuSuccess
import { GET_TEAM, GET_TEAM_CHILD } from "./actionTypes"
import { respGetteam, respGetteamChild } from "./actions"

//Include Both Helper File with needed methods
import { getTeam,getTeamChild} from "helpers/backend_helper"

// role
function* fetchGetTeam({ payload: req }) {
  try {
    const response = yield call(getTeam, req)
    if(response.status == 1){
      yield put(respGetteam(response))
    }else{
      yield put(respGetteam(response))
    }
  } catch (error) {
    console.log(error);
    yield put(respGetteam({"status" : 0, "message" : "Error Get Data"}))
  }
}

function* fetchGetTeamChild({ payload: req }) {
  try {
    const response = yield call(getTeamChild, req)
    if(response.status == 1){
      yield put(respGetteamChild(response))
    }else{
      yield put(respGetteamChild(response))
    }
  } catch (error) {
    console.log(error);
    yield put(respGetteamChild({"status" : 0, "message" : "Error Get Data"}))
  }
}

function* teamSaga() {
    
  yield takeEvery(GET_TEAM, fetchGetTeam)
  yield takeEvery(GET_TEAM_CHILD, fetchGetTeamChild)

}

export default teamSaga
