import {
    GET_MPLANT,
    RESP_MPLANT,
    MSGEDIT,
    MSGDELETE,
    MSGADD,
    RESET_MESSAGE,
    EDIT_PLANT,
    SAVE_PLANT,
    DELETE_PLANT,
  } from "./actionTypes"

  const INIT_STATE = {
    respGetmPlant: {},
    msgEdit: "",
    msgAdd: "",
    msgDelete: "",
  }

  const mstPlantReducer = (state = INIT_STATE, action) => {
    
    switch (action.type) {
      case GET_MPLANT:
        return {
          ...state,
        }
      case RESP_MPLANT:
        return {
          ...state,
          respGetmPlant: action.payload,
        }
      case MSGEDIT:
        return {
          ...state,
          msgEdit: action.payload,
        }
      case MSGDELETE:
          return {
            ...state,
            msgDelete: action.payload,
      }
      case MSGADD:
        return {
          ...state,
          msgAdd: action.payload,
      }
      case RESET_MESSAGE :
          return {
             ...state,
             msgEdit: "",
             msgAdd: "",
             msgDelete: "",
          }
          case EDIT_PLANT:
            return {
              ...state,
            }
          case SAVE_PLANT:
            return {
              ...state,
          }
          case DELETE_PLANT:
            return {
              ...state,
          }
      default:
        return state
    }
  }
  
  export default mstPlantReducer