import {
    GET_MPOSITION,
    RESP_MPOSITION,
    MSGEDIT,
    MSGDELETE,
    MSGADD,
    RESET_MESSAGE,
    EDIT_POSITION,SAVE_POSITION,DELETE_POSITION,
  } from "./actionTypes"

  export const getMstPositionData = (req) => ({
    type: GET_MPOSITION,
    payload: req,
  })
  
  export const respGetMp = resp => ({
    type: RESP_MPOSITION,
    payload: resp,
  })

  export const msgEdit = resp => ({
    type: MSGEDIT,
    payload: resp,
  })

  export const msgAdd = resp => ({
    type: MSGADD,
    payload: resp,
  })

  export const msgDelete = resp => ({
    type: MSGDELETE,
    payload: resp,
  })

  export const resetMessage = (resp) => ({
    type: RESET_MESSAGE,
    payload: resp,
  })
  
  export const editPosition = (req) => ({
    type: EDIT_POSITION,
    payload: req,
  })

  export const savePosition = (req) => ({
    type: SAVE_POSITION,
    payload: req,
  })

  export const deletePosition = (req) => ({
    type: DELETE_POSITION,
    payload: req,
  })