import {
    LOV_MENU_PARENT,
    LOV_USER,
    LOV_DIV,
    LOV_MENU,
    LOV_COMPANY,
    LOV_PLANT,
    LOV_POSITION,
    MSG_LOV,
  } from "./actionTypes"

  export const getMenuParent = req => ({
    type: LOV_MENU_PARENT,
    payload: req,
  })

  export const getUser = req => ({
    type: LOV_USER,
    payload: req,
  })

  export const getMenu = req => ({
    type: LOV_MENU,
    payload: req,
  })

  export const getDiv = req => ({
    type: LOV_DIV,
    payload: req,
  })

  export const getCompany = req => ({
    type: LOV_COMPANY,
    payload: req,
  })

  export const getPlant = req => ({
    type: LOV_PLANT,
    payload: req,
  })

  export const getPosition = req => ({
    type: LOV_POSITION,
    payload: req,
  })

  export const msgLov = (resp) => ({
    type: MSG_LOV,
    payload: resp,
  })
  
