import React, { useState, useEffect, useCallback } from "react";
import MetaTags from "react-meta-tags";

import BootstrapTable from "react-bootstrap-table-next";
import paginationFactory from "react-bootstrap-table2-paginator";

import Breadcrumbs from "../../components/Common/Breadcrumb";
import { useFormik } from "formik";
import * as Yup from "yup";
import Lov from "../../common/Lov";
import DeleteModal from "../../common/DeleteModal";
import '../../config';
import {
  Row,
  Col,
  Card,
  CardBody,
  Container,
  Button,
  UncontrolledTooltip,
  Label,
  Input,
  FormFeedback,
  Form,
  Spinner,
  FormGroup,
  CardHeader,
  UncontrolledAlert
} from "reactstrap";

import { getMenuAlldata, editMenu, resetMessage, saveMenu, deleteMenu } from "../../store/app002/actions"
import { getMenuParent } from "../../store/lov/actions"

import { useSelector, useDispatch } from "react-redux"

const Menu = () => {

  const dispatch = useDispatch();
  const [app002p01firstRenderDone, setApp002p01firstRenderDone] = useState(false);
  const [app002p01Page, setApp002p01Page] = useState(true)
  const [app002p02Page, setApp002p02Page] = useState(false)
  const [app002p03Page, setApp002p03Page] = useState(false)
  const [app002Msg, setApp002setMsg] = useState("")
  const [app002deleteModal, setApp002DeleteModal] = useState(false);

  useEffect(() => {
    dispatch(resetMessage());
  }, [dispatch])

  const app002CloseAllert = () => {
    setApp002setMsg("")
  }

  //p01
  const [app002p01TabelSearch, setApp002p01TabelSearch] = useState('');

  const [app002p01TabelMenuReq, setApp002p01TabelMenuReq] = useState({ page: 1, limit: 10, offset: 0, sort: "menuid", order: "asc", search: { any: app002p01TabelSearch } });

  const app002p01hendleTableChange = (type, { page, sortField, sortOrder, sizePerPage }) => {
    if (type === "sort") {
      setApp002p01TabelMenuReq({ page: 1, limit: sizePerPage, offset: 0, sort: sortField, order: sortOrder, search: { any: app002p01TabelSearch } });
    }
    if (type === "pagination") {
      setApp002p01TabelMenuReq({ page: page, limit: sizePerPage, offset: ((page - 1) * sizePerPage), sort: sortField, order: sortOrder, search: { any: app002p01TabelSearch } });
    }
  };

  useEffect(() => {
    setApp002p01firstRenderDone(true);
  }, []);

  useEffect(() => {
    setApp002p01TabelMenuReq({ page: 1, limit: 10, offset: 0, sort: "menuid", order: "asc", search: { any: app002p01TabelSearch } });
  }, [app002p01TabelSearch])

  useEffect(() => {
    if (app002p01firstRenderDone) {
      dispatch(getMenuAlldata(app002p01TabelMenuReq))
    }
  }, [app002p01TabelMenuReq])


  const app002p01MenuData = useSelector(state => {
    return state.MenuReduce.respGetMenus;
  });

  useEffect(() => {
    if (app002p01MenuData.status == "0") {
      setApp002setMsg(app002p01MenuData)
    }
  }, [app002p01MenuData])

  const app002p01TabelMenuColumns = [
    {
      dataField: "menuid",
      text: "Menu ID",
      sort: true,
      headerStyle: { textAlign: 'center' },
    },
    {
      dataField: "title",
      text: "Nama Menu",
      sort: true,
      headerStyle: { textAlign: 'center' },
    },
    {
      dataField: "moduleName",
      text: "Modul ID",
      sort: true,
      headerStyle: { textAlign: 'center' },
    },
    {
      dataField: "parent",
      text: "Parent Menu ID",
      sort: true,
      headerStyle: { textAlign: 'center' },
    },
    {
      dataField: "path",
      text: "URL",
      sort: true,
      headerStyle: { textAlign: 'center' },
    },
    {
      dataField: "icon",
      text: "ICON",
      sort: true,
      headerStyle: { textAlign: 'center' },
    },
    {
      dataField: "orderNo",
      text: "Order No",
      sort: true,
      hidden: true,
      headerStyle: { textAlign: 'center' },
    },
    {
      dataField: "bHide",
      text: "SHOW/HIDE",
      sort: true,
      hidden: true,
      headerStyle: { textAlign: 'center' },
    },
    {
      dataField: "action",
      isDummyField: true,
      text: "Action",
      headerStyle: { textAlign: 'center' },
      formatter: (cellContent, app002p01MenuData) => (
        <>
          <div style={{ justifyContent: 'center' }} className="d-flex gap-3">
            <i className="mdi mdi-pencil font-size-18  text-primary" id="edittooltip" onClick={() => app002p01PreEdit(app002p01MenuData)} />
            <UncontrolledTooltip placement="top" target="edittooltip">
              Edit
            </UncontrolledTooltip>

            <i className="mdi mdi-delete font-size-18 text-danger" id="deletetooltip" onClick={() => app002p01Delete(app002p01MenuData)} />
            <UncontrolledTooltip placement="top" target="deletetooltip">
              Delete
            </UncontrolledTooltip>

          </div>
        </>
      ),
    },
  ]

  const app002p01PreAdd = () => {
    app002p02ValidInput.setFieldValue("moduleName", "")
    app002p02ValidInput.setFieldValue("orderNo", 1)
    app002p02ValidInput.setFieldValue("parent", "")
    app002p02ValidInput.setFieldValue("menuid", "")
    app002p02ValidInput.setFieldValue("title", "")
    app002p02ValidInput.setFieldValue("path", "")
    app002p02ValidInput.setFieldValue("icon", "")
    app002p02ValidInput.setFieldValue("bHide", false)
    setApp002p02LovParentId("")
    setApp002setMsg("")
    setApp002p01Page(false)
    setApp002p02Page(true)
    app002p02ValidInput.setFieldValue("orderNo", 1)
  }

  const app002p01PreEdit = (app002p01MenuData) => {
    setApp002setMsg("")
    app002p03ValidInput.setFieldValue("moduleName", app002p01MenuData.moduleName)
    app002p03ValidInput.setFieldValue("orderNo", app002p01MenuData.orderNo != null ? app002p01MenuData.orderNo : 1)
    app002p03ValidInput.setFieldValue("parent", app002p01MenuData.parent)
    app002p03ValidInput.setFieldValue("menuid", app002p01MenuData.menuid)
    app002p03ValidInput.setFieldValue("title", app002p01MenuData.title)
    app002p03ValidInput.setFieldValue("path", app002p01MenuData.path)
    app002p03ValidInput.setFieldValue("icon", app002p01MenuData.icon)
    app002p03ValidInput.setFieldValue("bHide", app002p01MenuData.bHide != null ? app002p01MenuData.bHide : false)
    setApp002p01Page(false)
    setApp002p03Page(true)
  }

  const [app002p01MenuDelete, setApp002p01MenuDelete] = useState(null);

  const app002p01Delete = (app002p01MenuData) => {
    setApp002setMsg("")
    setApp002p01MenuDelete(app002p01MenuData);
    setApp002DeleteModal(true)
  }

  const app002HandleDeleteMenu = () => {
    if (app002p01MenuDelete.menuid) {
      dispatch(deleteMenu(app002p01MenuDelete));
      setApp002DeleteModal(false);
      setApp002p01MenuDelete(null)
    }
  }

  const app002p04Message = useSelector(state => {
    return state.MenuReduce.msgDelete;
  });

  useEffect(() => {
    if (app002p04Message.status == "1") {
      dispatch(getMenuAlldata(app002p01TabelMenuReq)); 
    }
    setApp002setMsg(app002p04Message);
  }, [app002p04Message])

  //p02
  const [app002p02Spinner, setApp002p02Spinner] = useState(false);
  const [app002p02MenuIdED, setApp002p02MenuIdED] = useState(false);
  const [app002p02LovParentId, setApp002p02LovParentId] = useState('');

  const app002p02ValidInput = useFormik({
    enableReinitialize: true,

    initialValues: {
      moduleName: '',
      orderNo: 1,
      parent: '',
      menuid: '',
      title: '',
      path: '',
      icon: '',
      bHide: false,

    },
    validationSchema: Yup.object().shape({
      moduleName: Yup.string()
        .required("Wajib diisi"),
      orderNo: Yup.number()
        .required("Wajib diisi")
        .max(99, "Max 2 chars"),
      title: Yup.string()
        .required("Wajib diisi"),
      path: Yup.string()
        .required("Wajib diisi"),
    }),


    onSubmit: (values) => {
      setApp002p02Spinner(true);
      setApp002setMsg("")
      dispatch(saveMenu(values));
    }
  });

  const app002p02Message = useSelector(state => {
    return state.MenuReduce.msgAdd;
  });

  useEffect(() => {
    if (app002p02Message.status == "1") {
      // app002p02ValidInput.setFieldValue( "moduleName", "")
      // app002p02ValidInput.setFieldValue( "orderNo", 1)
      // app002p02ValidInput.setFieldValue( "parent", "")
      // app002p02ValidInput.setFieldValue( "menuid", "")
      // app002p02ValidInput.setFieldValue( "title", "")
      // app002p02ValidInput.setFieldValue( "path", "")
      // app002p02ValidInput.setFieldValue( "icon", "")
      // app002p02ValidInput.setFieldValue( "bHide", false)
      setApp002p02LovParentId('')
      setApp002p01Page(true);
      setApp002p02Page(false);
      dispatch(getMenuAlldata(app002p01TabelMenuReq))
    }
    setApp002setMsg(app002p02Message)
    setApp002p02Spinner(false);
  }, [app002p02Message])

  const app002p02TabelMenuParentColumns = [
    {
      dataField: "parentMenuId",
      text: "Parent Menu ID",
      sort: true,
      headerStyle: { textAlign: 'center' },
    },
    {
      dataField: "parentTitle",
      text: "Parent Menu Name",
      sort: true,
      headerStyle: { textAlign: 'center' },
    },
  ]

  function app002p02callBackLovMenuParent(row) {
    if (row.parentMenuId != null && row.parentMenuId != "") {
      setApp002p02MenuIdED(true)
    } else {
      setApp002p02MenuIdED(false)
    }
    //app002p02ValidInput.setFieldValue( "menuid", row.menuid)
  }

  function app002p02ChangeFunc(val) {
    if (val != null && val != "") {
      setApp002p02MenuIdED(true)
    } else {
      setApp002p02MenuIdED(false)
    }
  }

  //p03
  const [app002p03Spinner, setApp002p03Spinner] = useState(false);
  const app002p03ValidInput = useFormik({
    enableReinitialize: true,

    initialValues: {
      moduleName: '',
      orderNo: null,
      parent: '',
      menuid: '',
      title: '',
      path: '',
      icon: '',
      bHide: false,

    },
    validationSchema: Yup.object().shape({
      moduleName: Yup.string()
        .required("Wajib diisi"),
      orderNo: Yup.number()
        .required("Wajib diisi")
        .max(99, "Max 2 chars"),
      title: Yup.string()
        .required("Wajib diisi"),
      path: Yup.string()
        .required("Wajib diisi"),
    }),


    onSubmit: (values) => {
      setApp002p03Spinner(true);
      setApp002setMsg("")
      dispatch(editMenu(values));
    }
  });

  const app002p03Message = useSelector(state => {
    return state.MenuReduce.msgEdit;
  });

  useEffect(() => {
    if (app002p03Message.status == "1") {
      setApp002p01Page(true);
      setApp002p03Page(false);
      dispatch(getMenuAlldata(app002p01TabelMenuReq))
    }
    setApp002setMsg(app002p03Message)
    setApp002p03Spinner(false);
  }, [app002p03Message])

  return (
    <React.Fragment>

      <div className="page-content">
        <MetaTags>
          <title>
            Korindo App
          </title>
        </MetaTags>

        <DeleteModal
          show={app002deleteModal}
          onDeleteClick={app002HandleDeleteMenu}
          onCloseClick={() => setApp002DeleteModal(false)}
        />

        {app002Msg ? <UncontrolledAlert toggle={app002CloseAllert} color={app002Msg.status == "1" ? "success" : "danger"}>
          {typeof app002Msg == 'string' ? app002Msg : app002Msg.listmessage?.map((msg, key) => (<p key={key}>{"* " + msg}</p>))}</UncontrolledAlert> : null}

        <Container style={{ display: app002p01Page ? 'block' : 'none' }} fluid={true}>
          <Breadcrumbs title="Forms" breadcrumbItem="Maintain Menu" />

          <Row>
            <Col>
              <Card>
                <CardHeader><i className="bx bx-list-check font-size-18 align-middle me-2"></i>List Menu</CardHeader>
                <CardBody>
                  <React.Fragment>
                    <Row className="mb-2">
                      <Col sm="2">
                        <div className="form-group m-0">
                          <div className="input-group">
                            <input
                              type="text"
                              className="form-control"
                              placeholder="Search ..."
                              value={app002p01TabelSearch}
                              onChange={e => { setApp002p01TabelSearch(e.target.value); }}
                            />
                            <div className="input-group-append">
                              <button className="btn btn-dark" type="button"
                                onClick={() => {
                                  dispatch(getMenuAlldata(app002p01TabelMenuReq))
                                }}>
                                <i className="mdi mdi-magnify" />
                              </button>
                            </div>
                          </div>
                        </div>
                      </Col>
                      <Col sm="10">
                        <div className="text-sm-end">
                          <button
                            type="button"
                            className="btn btn-primary "
                            onClick={() => { app002p01PreAdd() }}
                          >
                            <i className="bx bx-plus font-size-16 align-middle me-2"></i>{" "}
                            Add
                          </button>
                        </div>
                      </Col>
                    </Row>
                    <Row>
                      <BootstrapTable
                        wrapperClasses="table-responsive"
                        keyField="menuid"
                        rowClasses="text-nowrap"
                        remote={{ filter: true, pagination: true, sort: true, cellEdit: true }}
                        data={app002p01MenuData.data != null ? app002p01MenuData.data.menu : []}
                        columns={app002p01TabelMenuColumns}
                        pagination={paginationFactory({
                          page: app002p01TabelMenuReq.page,
                          sizePerPage: app002p01TabelMenuReq.limit,
                          sizePerPageList: [5, 10, 20],
                          totalSize: app002p01MenuData.data != null ? app002p01MenuData.data.menutotal : 0,
                          showTotal: true,
                        })}
                        classes={
                          "table align-middle table-nowrap"
                        }
                        onTableChange={app002p01hendleTableChange}
                        striped
                        hover
                        condensed
                      />
                    </Row>
                  </React.Fragment>
                </CardBody>
              </Card>
            </Col>
          </Row>
        </Container>

        {/* //p02 */}
        <Container style={{ display: app002p02Page ? 'block' : 'none' }} fluid={true}>
          <Breadcrumbs title="Forms" breadcrumbItem="Maintain Menu" />

          <Row>
            <Col lg={12}>
              <Card>
                <CardHeader><i className="bx bx-add-to-queue font-size-18 align-middle me-2"></i>Add Menu</CardHeader>
                <CardBody>
                  <Form
                    onSubmit={(e) => {
                      e.preventDefault();
                      app002p02ValidInput.handleSubmit();
                      return false;
                    }}>
                    <FormGroup className="mb-0">
                      <Row>
                        <Col md="5">
                          <div>
                            <Lov title="Parent Menu ID"
                              LenghtLabel=""
                              LenghtDiv="mb-3 col-sm-10"
                              columns={app002p02TabelMenuParentColumns}
                              getData={getMenuParent}
                              pageSize={10}
                              callbackFunc={app002p02callBackLovMenuParent}
                              onChangeFunc={app002p02ChangeFunc}
                              defaultSetInput="parentMenuId"
                              invalidData={app002p02ValidInput}
                              fieldValue="parent"
                              stateSearchInput={app002p02LovParentId}
                              stateSearchInputSet={setApp002p02LovParentId}
                              touchedLovField={app002p02ValidInput.touched.parent}
                              errorLovField={app002p02ValidInput.errors.parent}
                            />
                          </div>

                          <div className="mb-3 col-sm-10">
                            <Label>Menu ID</Label>
                            <Input
                              name="menuid"
                              type="text"
                              disabled={app002p02MenuIdED}
                              maxLength={6}
                              onChange={app002p02ValidInput.handleChange}
                              value={app002p02ValidInput.values.menuid || ""}
                            />
                          </div>

                          <div className="mb-3 col-sm-10">
                            <Label>Menu Name</Label>
                            <Input
                              name="title"
                              type="text"
                              maxLength={100}
                              onChange={app002p02ValidInput.handleChange}
                              value={app002p02ValidInput.values.title || ""}
                              invalid={
                                app002p02ValidInput.touched.title && app002p02ValidInput.errors.title ? true : false
                              }
                            />
                            {app002p02ValidInput.touched.title && app002p02ValidInput.errors.title ? (
                              <FormFeedback type="invalid">{app002p02ValidInput.errors.title}</FormFeedback>
                            ) : null}
                          </div>

                          <div className="mb-3 col-sm-10">
                            <Label>Modul ID</Label>
                            <Input
                              name="moduleName"
                              type="text"
                              maxLength={20}
                              onChange={app002p02ValidInput.handleChange}
                              value={app002p02ValidInput.values.moduleName || ""}
                              invalid={
                                app002p02ValidInput.touched.moduleName && app002p02ValidInput.errors.moduleName ? true : false
                              }
                            />
                            {app002p02ValidInput.touched.moduleName && app002p02ValidInput.errors.moduleName ? (
                              <FormFeedback type="invalid">{app002p02ValidInput.errors.moduleName}</FormFeedback>
                            ) : null}
                          </div>

                        </Col>
                        <Col md="5">

                          <div className="mb-3 col-sm-10">
                            <Label>Path</Label>
                            <Input
                              name="path"
                              type="text"
                              maxLength={70}
                              onChange={app002p02ValidInput.handleChange}
                              value={app002p02ValidInput.values.path || ""}
                              invalid={
                                app002p02ValidInput.touched.path && app002p02ValidInput.errors.path ? true : false
                              }
                            />
                            {app002p02ValidInput.touched.path && app002p02ValidInput.errors.path ? (
                              <FormFeedback type="invalid">{app002p02ValidInput.errors.path}</FormFeedback>
                            ) : null}
                          </div>

                          <div className="mb-3 col-sm-10">
                            <Label>Icon</Label>
                            <Input
                              name="icon"
                              type="text"
                              maxLength={50}
                              onChange={app002p02ValidInput.handleChange}
                              value={app002p02ValidInput.values.icon || ""}
                            />
                          </div>

                          <div className="mb-3 col-sm-3">
                            <Label>Order</Label>
                            <Input
                              name="orderNo"
                              type="number"
                              onChange={app002p02ValidInput.handleChange}
                              value={app002p02ValidInput.values.orderNo || ""}
                              invalid={
                                app002p02ValidInput.touched.orderNo && app002p02ValidInput.errors.orderNo ? true : false
                              }
                            />
                            {app002p02ValidInput.touched.orderNo && app002p02ValidInput.errors.orderNo ? (
                              <FormFeedback type="invalid">{app002p02ValidInput.errors.orderNo}</FormFeedback>
                            ) : null}
                          </div>

                          <br />
                          <div className="mb-3 col-sm-10">
                            <div className="form-check">
                              <Input
                                className="form-check-input"
                                type="checkbox"
                                onClick={e => app002p02ValidInput.setFieldValue('bHide', e.target.checked ? false : true)}
                                checked={app002p02ValidInput.values.bHide} />
                              <label className="form-check-label" htmlFor="autoSizingCheck">
                                Hide Menu
                              </label>
                            </div>
                          </div>

                        </Col>


                      </Row>
                      <Button type="submit" color="primary" className="ms-1">
                        <i className="bx bxs-save align-middle me-2"></i>{" "}
                        Submit
                      </Button>
                      <Spinner style={{ display: app002p02Spinner ? "block" : "none", marginTop: '-35px' }} className="ms-4" color="danger" />
                    </FormGroup>

                  </Form>

                </CardBody>
              </Card>
            </Col>


          </Row>
          <div className="text-sm-end">
            <button
              type="button"
              className="btn btn-danger "
              onClick={() => { setApp002p01Page(true); setApp002p02Page(false); setApp002setMsg("") }}
            >
              <i className="bx bx-arrow-back align-middle me-2"></i>{" "}
              Back
            </button>
          </div>
        </Container>



        {/* //p03                       */}
        <Container style={{ display: app002p03Page ? 'block' : 'none' }} fluid={true}>
          <Breadcrumbs title="Forms" breadcrumbItem="Maintain Menu" />

          <Row>
            <Col lg={12}>
              <Card>
                <CardHeader><i className="bx bxs-edit-alt font-size-18 align-middle me-2"></i>Edit Menu</CardHeader>
                <CardBody>
                  <Form
                    onSubmit={(e) => {
                      e.preventDefault();
                      app002p03ValidInput.handleSubmit();
                      return false;
                    }}>
                    <FormGroup className="mb-0">
                      <Row>
                        <Col md="5">
                          <div className="mb-3 col-sm-10">
                            <Label>Parent Menu ID</Label>
                            <Input
                              name="parent"
                              type="text"
                              maxLength={20}
                              disabled
                              onChange={app002p03ValidInput.handleChange}
                              value={app002p03ValidInput.values.parent || ""}
                            />
                          </div>

                          <div className="mb-3 col-sm-10">
                            <Label>Menu ID</Label>
                            <Input
                              name="menuid"
                              disabled
                              type="text"
                              maxLength={6}
                              onChange={app002p03ValidInput.handleChange}
                              value={app002p03ValidInput.values.menuid || ""}
                            />
                          </div>

                          <div className="mb-3 col-sm-10">
                            <Label>Menu Name</Label>
                            <Input
                              name="title"
                              type="text"
                              maxLength={100}
                              onChange={app002p03ValidInput.handleChange}
                              value={app002p03ValidInput.values.title || ""}
                              invalid={
                                app002p03ValidInput.touched.title && app002p03ValidInput.errors.title ? true : false
                              }
                            />
                            {app002p03ValidInput.touched.title && app002p03ValidInput.errors.title ? (
                              <FormFeedback type="invalid">{app002p03ValidInput.errors.title}</FormFeedback>
                            ) : null}
                          </div>

                          <div className="mb-3 col-sm-10">
                            <Label>Modul ID</Label>
                            <Input
                              name="moduleName"
                              type="text"
                              maxLength={20}
                              onChange={app002p03ValidInput.handleChange}
                              value={app002p03ValidInput.values.moduleName || ""}
                              invalid={
                                app002p03ValidInput.touched.moduleName && app002p03ValidInput.errors.moduleName ? true : false
                              }
                            />
                            {app002p03ValidInput.touched.moduleName && app002p03ValidInput.errors.moduleName ? (
                              <FormFeedback type="invalid">{app002p03ValidInput.errors.moduleName}</FormFeedback>
                            ) : null}
                          </div>

                        </Col>
                        <Col md="5">

                          <div className="mb-3 col-sm-10">
                            <Label>Path</Label>
                            <Input
                              name="path"
                              type="text"
                              maxLength={70}
                              onChange={app002p03ValidInput.handleChange}
                              value={app002p03ValidInput.values.path || ""}
                              invalid={
                                app002p03ValidInput.touched.path && app002p03ValidInput.errors.path ? true : false
                              }
                            />
                            {app002p03ValidInput.touched.path && app002p03ValidInput.errors.path ? (
                              <FormFeedback type="invalid">{app002p03ValidInput.errors.path}</FormFeedback>
                            ) : null}
                          </div>

                          <div className="mb-3 col-sm-10">
                            <Label>Icon</Label>
                            <Input
                              name="icon"
                              type="text"
                              maxLength={50}
                              onChange={app002p03ValidInput.handleChange}
                              value={app002p03ValidInput.values.icon || ""}
                            />
                          </div>

                          <div className="mb-3 col-sm-3">
                            <Label>Order</Label>
                            <Input
                              name="orderNo"
                              type="number"
                              onChange={app002p03ValidInput.handleChange}
                              value={app002p03ValidInput.values.orderNo || ""}
                              invalid={
                                app002p03ValidInput.touched.orderNo && app002p03ValidInput.errors.orderNo ? true : false
                              }
                            />
                            {app002p03ValidInput.touched.orderNo && app002p03ValidInput.errors.orderNo ? (
                              <FormFeedback type="invalid">{app002p03ValidInput.errors.orderNo}</FormFeedback>
                            ) : null}
                          </div>

                          <br />
                          <div className="mb-3 col-sm-10">
                            <div className="form-check">
                              <input
                                className="form-check-input"
                                type="checkbox"
                                onClick={e => app002p03ValidInput.setFieldValue('bHide', e.target.checked ? false : true)}
                                checked={app002p03ValidInput.values.bHide}
                              />
                              <label className="form-check-label" htmlFor="autoSizingCheck">
                                Hide Menu
                              </label>
                            </div>
                          </div>

                        </Col>


                      </Row>
                      <Button type="submit" color="primary" className="ms-1">
                        <i className="bx bxs-save align-middle me-2"></i>{" "}
                        Submit
                      </Button>
                      <Spinner style={{ display: app002p03Spinner ? "block" : "none", marginTop: '-35px' }} className="ms-4" color="danger" />
                    </FormGroup>

                  </Form>

                </CardBody>
              </Card>
            </Col>


          </Row>

          <div className="text-sm-end">
            <button
              type="button"
              className="btn btn-danger justify-right"
              onClick={() => { setApp002p01Page(true); setApp002p03Page(false); setApp002setMsg("") }}
            >
              <i className="bx bx-arrow-back align-middle me-2"></i>{" "}
              Back
            </button>
          </div>

        </Container>

      </div>



    </React.Fragment>
  );
};

export default Menu  
