import { call, put, takeEvery} from "redux-saga/effects"

import { GET_COMP, EDIT_COMP, SAVE_COMP, DELETE_COMP } from "./actionTypes"
import { respGetCompany, msgEdit, msgAdd, msgDelete } from "./actions"

import { getCompany, editCompany, deleteCompany, saveCompany } from "helpers/backend_helper"

function* fetchGetCompany ({ payload: req }) {
  try {
    const response = yield call(getCompany, req)
    if(response.status == 1){
      yield put(respGetCompany(response))
    }else{
      yield put(respGetCompany(response))
    }
  } catch (error) {
    console.log(error);
    yield put(respGetCompany({"status" : 0, "message" : "Error Get Data"}))
  }
}

function* fetchEditCompany ({ payload: req }) {
  try {
    const response = yield call(editCompany, req)
    yield put(msgEdit(response))
  } catch (error) {
    console.log(error);
    yield put(msgEdit({"status" : 0, "message" : "Error Edit Data"}))
  }
}

function* fetchSaveCompany ({ payload: req }) {
  try {
    const response = yield call(saveCompany, req)
    yield put(msgAdd(response))
  } catch (error) {
    yield put(msgAdd({"status" : 0, "message" : "Error Save Data"}))
  }
}

function* fetchDeleteCompany ({ payload: req }) {
  try {
    const response = yield call(deleteCompany, req)
    yield put(msgDelete(response))
  } catch (error) {
    console.log(error);
    yield put(msgDelete({"status" : 0, "message" : "Error Delete Data"}))
  }
}

function* roCompanySaga () {
    
  yield takeEvery(GET_COMP, fetchGetCompany)
  yield takeEvery(EDIT_COMP, fetchEditCompany)
  yield takeEvery(SAVE_COMP, fetchSaveCompany)
  yield takeEvery(DELETE_COMP, fetchDeleteCompany)

}

export default roCompanySaga
