import {
    GET_COMP,
    RESP_COMP,
    MSGEDIT,
    MSGDELETE,
    MSGADD,
    RESET_MESSAGE,
    EDIT_COMP,
    SAVE_COMP,
    DELETE_COMP,
  } from "./actionTypes"

  const INIT_STATE = {
    respGetComp: {},
    msgEdit: "",
    msgAdd: "",
    msgDelete: "",
  }

  const compReduce = (state = INIT_STATE, action) => {
    
    switch (action.type) {
      case GET_COMP:
        return {
          ...state,
        }
      case RESP_COMP:
        return {
          ...state,
          respGetComp: action.payload,
        }
      case MSGEDIT:
        return {
          ...state,
          msgEdit: action.payload,
        }
      case MSGDELETE:
          return {
            ...state,
            msgDelete: action.payload,
      }
      case MSGADD:
        return {
          ...state,
          msgAdd: action.payload,
      }
      case RESET_MESSAGE :
          return {
             ...state,
             msgEdit: "",
             msgAdd: "",
             msgDelete: "",
          }
      case EDIT_COMP:
        return {
          ...state,
        }
      case SAVE_COMP:
        return {
          ...state,
      }
      case DELETE_COMP:
        return {
          ...state,
      }
      
      default:
        return state
    }
  }
  
  export default compReduce