import React, { useState, useEffect } from "react";
import MetaTags from "react-meta-tags";
import Breadcrumbs from "../../components/Common/Breadcrumb";
import BootstrapTable from "react-bootstrap-table-next";
import { useFormik, } from "formik";
import paginationFactory from "react-bootstrap-table2-paginator";

import * as Yup from "yup";
import { getSetting,editSetting,resetMessage,saveSetting } from "../../store/app010/actions"
import {
    Row,
    Col,
    Card,
    CardBody,
    Container,
    Button,
    UncontrolledTooltip,
    Label,
    Input,
    FormFeedback,
    Form,
    Spinner,
    FormGroup,
    CardHeader,
    UncontrolledAlert,
  } from "reactstrap";

  import { useSelector, useDispatch } from "react-redux"

const MstDtlsettings = () => {

    const dispatch = useDispatch();

    const [app010p01firstRenderDone, setApp010p01firstRenderDone] = useState(false);
    const [app010p01Page, setApp010p01Page] = useState(true)
    const [app010p02Page, setApp010p02Page] = useState(false)
    const [app010p03Page, setApp010p03Page] = useState(false)
    const [app010Msg, setApp010setMsg] = useState("")

    useEffect(() => {
        dispatch(resetMessage());
        
      }, [dispatch])
    
      const app010CloseAllert = () => {
        setApp010setMsg("")
      }

       //p01
        const [app010p01TabelSearch, setApp010p01TabelSearch] = useState('');
        const [app010p01TabelMstDtlsettingsReq, setApp010p01TabelMstDtlsettingsReq] = useState({page : 1, limit: 10, offset: 0, sort: "vitemcode", order: "asc", search: {any : app010p01TabelSearch}});
        
        const app010p01hendleTableChange = (type, {page, sortField, sortOrder, sizePerPage}) => {   
            if(type === "sort"){
            setApp010p01TabelMstDtlsettingsReq({page : 1, limit: sizePerPage, offset: 0, sort: sortField, order: sortOrder, search: {any : app010p01TabelSearch}});
            }
            if(type==="pagination"){
            setApp010p01TabelMstDtlsettingsReq({page : page, limit: sizePerPage, offset: ((page-1) * sizePerPage), sort: sortField, order: sortOrder, search: {any : app010p01TabelSearch}});
            }
        };
        
        useEffect(() => {
            setApp010p01firstRenderDone(true);
          }, []);
        
          useEffect(() => {
            if(app010p01firstRenderDone){
              dispatch(getSetting(app010p01TabelMstDtlsettingsReq))
            }
          }, [app010p01TabelMstDtlsettingsReq])

          useEffect(() => {
            setApp010p01TabelMstDtlsettingsReq({page : 1, limit: 10, offset: 0, sort: "", order: "asc", search: {any : app010p01TabelSearch}});
          }, [app010p01TabelSearch])
        
          const app010p01MstDtlsettingsData  = useSelector(state => {
             return state.MstDtlsettingsReducer.respSettings;
          });

          useEffect(() => {
            if(app010p01MstDtlsettingsData.status == "0"){    
              setApp010setMsg(app010p01MstDtlsettingsData)
            }
          }, [app010p01MstDtlsettingsData])

          useEffect(() => {
            if(app010p01firstRenderDone){
              dispatch(getSetting(app010p01TabelMstDtlsettingsReq))
            }
          }, [app010p01TabelMstDtlsettingsReq])

          const app010p01TabelMstDtlsettingsColumns = [
            {
              dataField: "id",
              text: "ID",
              sort: true,
              headerStyle: { textAlign: 'center' },
               hidden: true,
            },
            {
              dataField: "vitemcode",
              text: "Code",
              sort: true,
              headerStyle: { textAlign: 'center' },
            },
            {
              dataField: "vitemname",
              text: "Name",
              sort: true,
              headerStyle: { textAlign: 'center' },
            },
            {
              dataField: "vitemdesc",
              text: "Description",
              sort: true,
              headerStyle: { textAlign: 'center' },
             
            },
            {
                dataField: "level",
                text: "Level",
                sort: true,
                headerStyle: { textAlign: 'center' },
                align: 'center',

              },
              {
                dataField: "active",
                text: "Active",
                sort: true,
                hidden : true,
                headerStyle: { textAlign: 'center' },
              }, 

              {
                dataField: "activeDesc",
                text: "Active",
                sort: true,
                headerStyle: { textAlign: 'center' },
                align: 'center',
              }, 
            {
              dataField: "action",
              isDummyField: true,
              text: "Action",
              headerStyle: { textAlign: 'center' },
              formatter: (cellContent, app010p01MstDtlsettingsData) => (
                <>
                  <div style={{ justifyContent: 'center' }} className="d-flex gap-3">
                    <i className="mdi mdi-pencil font-size-18  text-primary" id="edittooltip" onClick={() => app010p01PreEdit(app010p01MstDtlsettingsData)} />
                    <UncontrolledTooltip placement="top" target="edittooltip">
                      Edit
                    </UncontrolledTooltip>
        
                  </div>
                </>
              ),
            },
          ]
        
          const app010p01PreAdd = () => {
            app010p02ValidInput.setFieldValue("vitemcode", "")
            app010p02ValidInput.setFieldValue("vitemname", "")
            app010p02ValidInput.setFieldValue("vitemdesc", "")
            app010p02ValidInput.setFieldValue("level", "")
    
            setApp010setMsg("")
            setApp010p01Page(false)
            setApp010p02Page(true)
          }
        
          const app010p01PreEdit = (app010p01MstDtlsettingsData) => {
            setApp010setMsg("")
            app010p03ValidInput.setFieldValue( "id", app010p01MstDtlsettingsData.id)
            app010p03ValidInput.setFieldValue( "vitemcode", app010p01MstDtlsettingsData.vitemcode)
            app010p03ValidInput.setFieldValue( "vitemname", app010p01MstDtlsettingsData.vitemname)
            app010p03ValidInput.setFieldValue( "vitemdesc", app010p01MstDtlsettingsData.vitemdesc)
            app010p03ValidInput.setFieldValue( "level", app010p01MstDtlsettingsData.level)
            app010p03ValidInput.setFieldValue( "active", app010p01MstDtlsettingsData.active)
       
            setApp010p01Page(false)
            setApp010p03Page(true)
          }

          const app010p01Message  = useSelector(state => {
            return state.MstDtlsettingsReducer.msgAdd; 
          });
        
          useEffect(() => {
            if(app010p01Message.status == "1"){
              dispatch(getSetting(app010p01TabelMstDtlsettingsReq))
            }
            setApp010setMsg(app010p01Message);
          }, [app010p01Message])

        //p02
        const [app010p02Spinner, setApp010p02Spinner] = useState(false);

        const app010p02ValidInput = useFormik({
            enableReinitialize: true,
          
            initialValues: {
              vitemcode: '',
              vitemname: '', 
              vitemdesc: '',
              level: '',
              active: true
            },
            validationSchema: Yup.object().shape({
              vitemcode: Yup.string().required("Wajib diisi"),
              vitemname: Yup.string().required("Wajib diisi"),
              vitemdesc: Yup.string().required("Wajib diisi"),
            }),
 
            onSubmit: (values) => {
                setApp010p02Spinner(true);
                setApp010setMsg("")
                dispatch(saveSetting(values));
              }
            });

            const app010p02Message = useSelector(state => {
                return state.MstDtlsettingsReducer.msgAdd;
              });  

              useEffect(() => {
                if (app010p02Message.status == "1") {
                  dispatch(getSetting(app010p01TabelMstDtlsettingsReq))
                  setApp010p01Page(true);
                  setApp010p02Page(false);
                }
                setApp010setMsg(app010p02Message)
                setApp010p02Spinner(false);
              }, [app010p02Message])  


            //p03
                const [app010p03Spinner, setApp010p03Spinner] = useState(false);
                const app010p03ValidInput = useFormik({
                enableReinitialize: true,
                
                initialValues: {
                    vitemcode: '',
                    vitemname : '',
                    vitemdesc : '',
                    level: '',
                    active: true,
                },

                validationSchema: Yup.object().shape({
                    vitemcode: Yup.string().required("Wajib diisi"),
                    vitemname: Yup.string().required("Wajib diisi"),
                    vitemdesc: Yup.string().required("Wajib diisi"),
                }),

                
                onSubmit: (values) => {
                    setApp010p03Spinner(true);
                    setApp010setMsg("")
                    dispatch(editSetting(values));
                }
                });
  
                const app010p03Message  = useSelector(state => {
                  return state.MstDtlsettingsReducer.msgEdit;
                });
              
                useEffect(() => {
                  if(app010p03Message.status == "1"){
                    setApp010p01Page(true); 
                    setApp010p03Page(false);
                    dispatch(getSetting(app010p01TabelMstDtlsettingsReq))
                  }
                  setApp010setMsg(app010p03Message)
                  setApp010p03Spinner(false);
                }, [app010p03Message])

      return (
        <React.Fragment>
          
          <div className="page-content">
            <MetaTags>
              <title>
              Korindo App
              </title>
            </MetaTags>

             {app010Msg !== ""? <UncontrolledAlert toggle={app010CloseAllert} color={app010Msg.status == "1" ? "success" : "danger"}>
                  {typeof app010Msg == 'string' ? app010Msg : app010Msg.listmessage?.map((msg, key) => ( <p key={key}>{"* " +msg}</p>))}</UncontrolledAlert> : null}

                <Container style={{display : app010p01Page ? 'block' : 'none'}} fluid={true}>
                    <Breadcrumbs title="Forms" breadcrumbItem="Master Detail Setting" />
                    <Row>
            <Col>
              <Card>
              <CardHeader><i className="bx bx-list-check font-size-18 align-middle me-2"></i>List Master Detail Setting</CardHeader>
                 <CardBody>
                 <React.Fragment>
                             <Row className="mb-2">
                               <Col sm="2">
                                 <div className="form-group m-0">
                                   <div className="input-group">
                                   <input
                                       type="text"
                                       className="form-control"
                                       placeholder="Search ..."
                                       value={app010p01TabelSearch}
                                       onChange={ e  => {setApp010p01TabelSearch(e.target.value);} } 
                                     />
                                     <div className="input-group-append">
                                       <button className="btn btn-dark" type="button" 
                                          onClick={() => {
                                            dispatch(getSetting(app010p01TabelMstDtlsettingsReq))
                                                         }}>
                                         <i className="mdi mdi-magnify" />
                                       </button>
                                     </div>
                                   </div>
                                 </div>
                               </Col>
                               <Col sm="10">
                                 <div className="text-sm-end">
                                 <button
                                       type="button"
                                       className="btn btn-primary "
                                        onClick= {() =>  { app010p01PreAdd()}}
                               >  
                                   <i className="bx bx-plus font-size-16 align-middle me-2"></i>{" "}
                                   Add 
                                 </button>
                                 </div>
                               </Col>
                             </Row>
                           <Row>
                            
                           <BootstrapTable
                              wrapperClasses="table-responsive"
                              keyField = "id"
                              rowClasses="text-nowrap"
                              remote ={{ filter: true, pagination: true, sort: true, cellEdit: true }}
                              data = {app010p01MstDtlsettingsData.data != null ? app010p01MstDtlsettingsData.data.dtlsetting : []}
                              
                              columns = {app010p01TabelMstDtlsettingsColumns}
                              pagination = {paginationFactory ({
                                  page : app010p01TabelMstDtlsettingsReq.page,
                                  sizePerPage : app010p01TabelMstDtlsettingsReq.limit,
                                  sizePerPageList : [5, 10,20],
                                  totalSize : app010p01MstDtlsettingsData.data != null ? app010p01MstDtlsettingsData.data.dtlsettingtotal : 0,
                                  showTotal: true,
                              })}
                              classes={
                                "table align-middle table-nowrap"
                              }
                              onTableChange = {app010p01hendleTableChange}
                              striped
                              hover
                              condensed
                            />

                           </Row>
                  </React.Fragment>
                </CardBody>
              </Card>
            </Col>
          </Row>
        </Container>

        {/* //p02 */}

          <Container style={{ display: app010p02Page ? 'block' : 'none' }} fluid={true}>
          <Breadcrumbs title="Forms" breadcrumbItem="Master Detail Setting" />

          <Row>
            <Col lg={12}>
              <Card>
                <CardHeader><i className="bx bx-add-to-queue font-size-18 align-middle me-2"></i> Add Master Detail Setting</CardHeader>
                <CardBody>
                <Form
                    onSubmit={(e) => {
                      e.preventDefault();
                      app010p02ValidInput.handleSubmit();
                      return false;
                    }}>
                    <FormGroup className="mb-0">
                    <div className="mb-3 col-sm-3">
                        <Label>Code</Label>
                        <Input
                          name="vitemcode"
                          type="text"
                          maxLength={100}
                          onChange={app010p02ValidInput.handleChange}
                          value={app010p02ValidInput.values.vitemcode || ""}
                          invalid={
                            app010p02ValidInput.touched.vitemcode && app010p02ValidInput.errors.vitemcode ? true : false
                          }
                        />
                        {app010p02ValidInput.touched.vitemcode && app010p02ValidInput.errors.vitemcode ? (
                          <FormFeedback type="invalid">{app010p02ValidInput.errors.vitemcode}</FormFeedback>
                        ) : null}
                      </div>
                      <div className="mb-3 col-sm-3">
                        <Label>Name</Label>
                        <Input
                          name="vitemname"
                          type="text"
                          maxLength={100}
                          onChange={app010p02ValidInput.handleChange}
                          value={app010p02ValidInput.values.vitemname || ""}
                          invalid={
                            app010p02ValidInput.touched.vitemname && app010p02ValidInput.errors.vitemname ? true : false
                          }
                        />
                        {app010p02ValidInput.touched.vitemname && app010p02ValidInput.errors.vitemname ? (
                          <FormFeedback type="invalid">{app010p02ValidInput.errors.vitemname}</FormFeedback>
                        ) : null}
                      </div>   
                      <div className="mb-3 col-sm-3">
                        <Label>Description</Label>
                        <Input
                          name="vitemdesc"
                          type="text"
                          maxLength={200}
                          onChange={app010p02ValidInput.handleChange}
                          value={app010p02ValidInput.values.vitemdesc || ""}
                          invalid={
                            app010p02ValidInput.touched.vitemdesc && app010p02ValidInput.errors.vitemdesc ? true : false
                          }
                        />
                        {app010p02ValidInput.touched.vitemdesc && app010p02ValidInput.errors.vitemdesc ? (
                          <FormFeedback type="invalid">{app010p02ValidInput.errors.vitemdesc}</FormFeedback>
                        ) : null}
                      </div>  
                      <div className="mb-3 col-sm-3">
                        <Label>Level</Label>
                        <Input
                          name="level"
                          type="text"
                          maxLength={2}
                          onChange={app010p02ValidInput.handleChange}
                          value={app010p02ValidInput.values.level || ""}
                          invalid={
                            app010p02ValidInput.touched.level && app010p02ValidInput.errors.level ? true : false
                          }
                        />
                        {app010p02ValidInput.touched.level && app010p02ValidInput.errors.level ? (
                          <FormFeedback type="invalid">{app010p02ValidInput.errors.level}</FormFeedback>
                        ) : null}
                      </div>


                      <div className="mb-3 col-sm-3">
                        <div id="my-radio-group">Active</div>
                        <div className="square-switch">
                            <Input
                              id="app010p02active"
                              type="checkbox"
                              switch="none"
                              onClick={e => app010p02ValidInput.setFieldValue('active', e.target.checked ? false : true)}
                              checked={app010p02ValidInput.values.active}
                            />
                            <label
                              htmlFor="app010p02active"
                              data-on-label="On"
                              data-off-label="Off"
                            />
                          </div>
                      </div>
                      
                      <Button type="submit" color="primary" className="ms-1">
                        <i className="bx bxs-save align-middle me-2"></i>{" "}
                        Submit
                      </Button>
                      <Spinner style={{ display: app010p02Spinner ? "block" : "none", marginTop: '-35px' }} className="ms-4" color="danger" />
                    </FormGroup>

                    </Form>
                  </CardBody>
              </Card>
            </Col>
          </Row>
          <div className="text-sm-end">
            <button
              type="button"
              className="btn btn-danger "
              onClick={() => { setApp010p01Page(true); setApp010p02Page(false); setApp010setMsg("") }}
            >
              <i className="bx bx-arrow-back align-middle me-2"></i>{" "}
              Back
            </button>
          </div>
        </Container>

          {/* //p03 */}
          <Container style={{display : app010p03Page ? 'block' : 'none'}} fluid={true}>
          <Breadcrumbs title="Forms" breadcrumbItem="Master Detail Setting" />

          <Row>
            <Col lg={12}>
              <Card>
                <CardHeader><i className="bx bxs-edit-alt font-size-18 align-middle me-2"></i>Edit Master Detail Setting</CardHeader>
                <CardBody>
                  <Form
                    onSubmit={(e) => {
                      e.preventDefault();
                      app010p03ValidInput.handleSubmit();
                      return false;
                    }}>
                    <FormGroup className="mb-0">

                    <div className="mb-3 col-sm-3">
                            <Label>Code</Label>
                            <Input
                              name="vitemcode"
                              type="text"
                              disabled
                              maxLength={100}
                              onChange={app010p03ValidInput.handleChange}
                              value={app010p03ValidInput.values.vitemcode || ""}
                              invalid={
                                app010p03ValidInput.touched.vitemcode && app010p03ValidInput.errors.vitemcode ? true : false
                              }
                            />
                            {app010p03ValidInput.touched.vitemcode && app010p03ValidInput.errors.vitemcode ? (
                              <FormFeedback type="invalid">{app010p03ValidInput.errors.vitemcode}</FormFeedback>
                            ) : null}
                      </div>

                      <div className="mb-3 col-sm-3">
                            <Label>Name</Label>
                            <Input
                              name="vitemname"
                              type="text"
                              maxLength={100}
                              onChange={app010p03ValidInput.handleChange}
                              value={app010p03ValidInput.values.vitemname || ""}
                              invalid={
                                app010p03ValidInput.touched.vitemname && app010p03ValidInput.errors.vitemname ? true : false
                              }
                            />
                            {app010p03ValidInput.touched.vitemname && app010p03ValidInput.errors.vitemname ? (
                              <FormFeedback type="invalid">{app010p03ValidInput.errors.vitemname}</FormFeedback>
                            ) : null}
                      </div>
                      <div className="mb-3 col-sm-3">
                        <Label>Description</Label>
                        <Input
                          name="vitemdesc"
                          type="text"
                          maxLength={200}
                          onChange={app010p03ValidInput.handleChange}
                          value={app010p03ValidInput.values.vitemdesc || ""}
                          invalid={
                            app010p03ValidInput.touched.vitemdesc && app010p03ValidInput.errors.vitemdesc ? true : false
                          }
                        />
                        {app010p03ValidInput.touched.vitemdesc && app010p03ValidInput.errors.vitemdesc ? (
                          <FormFeedback type="invalid">{app010p03ValidInput.errors.vitemdesc}</FormFeedback>
                        ) : null}
                      </div>
                      <div className="mb-3 col-sm-3">
                        <Label>Level</Label>
                        <Input
                          name="level"
                          type="text"
                          maxLength={2}
                          onChange={app010p03ValidInput.handleChange}
                          value={app010p03ValidInput.values.level || ""}
                          invalid={
                            app010p03ValidInput.touched.level && app010p03ValidInput.errors.level ? true : false
                          }
                        />
                        {app010p03ValidInput.touched.level && app010p03ValidInput.errors.level ? (
                          <FormFeedback type="invalid">{app010p03ValidInput.errors.level}</FormFeedback>
                        ) : null}
                      </div>

                      <div className="mb-3 col-sm-3">
                          <div id="my-radio-group">Active</div>
                          <div className="square-switch">
                            <Input
                              id="app010p03active"
                              type="checkbox"
                              switch="none"
                              onClick={e => app010p03ValidInput.setFieldValue('active', e.target.checked ? false : true)}
                              checked={app010p03ValidInput.values.active}
                            />
                            <label
                              htmlFor="app010p03active"
                              data-on-label="On"
                              data-off-label="Off"
                            />
                          </div>
                  
                        </div> 

                          <Button type="submit" color="primary" className="ms-1">
                              <i className="bx bxs-save align-middle me-2"></i>{" "}
                                  Submit
                              </Button>
                          <Spinner style={{ display: app010p03Spinner ? "block" : "none", marginTop : '-35px'}} className="ms-4"  color="danger" />
                         </FormGroup>
                    
                    </Form>
                        
                    </CardBody>
                  </Card>
                </Col>
              </Row>
              <div className="text-sm-end">
                <button
                    type="button"
                  className="btn btn-danger "
                  onClick ={() => { setApp010p01Page(true); setApp010p03Page(false); setApp010setMsg("")}}
              >
              <i className="bx bx-arrow-back align-middle me-2"></i>{" "}
              Back
            </button>
          </div>
        </Container>

         </div>  

            </React.Fragment>  
      )
}
export default MstDtlsettings;