import React, { useState, useEffect } from "react";
import MetaTags from "react-meta-tags"; 
import BootstrapTable from "react-bootstrap-table-next";
import paginationFactory from "react-bootstrap-table2-paginator";
import { useFormik } from "formik";
import * as Yup from "yup";
import Breadcrumbs from "../../components/Common/Breadcrumb";
import DeleteModal from "../../common/DeleteModal";
import Lov from "../../common/Lov";
import { getCombo } from "../../store/combo/actions"
import { getPlant } from "../../store/lov/actions"
import { useSelector, useDispatch } from "react-redux"
import '../../config';
import {
    Row,
    Col,
    Card,
    CardBody,
    Container,
    Button,
    UncontrolledTooltip,
    Label,
    Input,
    FormFeedback,
    Form,
    Spinner,
    FormGroup,
    CardHeader,
    UncontrolledAlert,
    } from "reactstrap";

    import { getDivisiData, editDivisi, saveDivisi, deleteDivisi, resetMessage } from "../../store/app011/actions"
    

    const Divisi = () => { 

      const dispatch = useDispatch();  
      const [app011p01firstRenderDone, setApp011p01firstRenderDone] = useState(false);
      const [app011p01Page, setApp011p01Page] = useState(true)
      const [app011p02Page, setApp011p02Page] = useState(false)
      const [app011p03Page, setApp011p03Page] = useState(false)
      const [app011Msg, setApp011setMsg] = useState("")
      const [app011DeleteModal, setApp011DeleteModal] = useState(false);

        useEffect(() => {
          dispatch(resetMessage());
        }, [dispatch])
      
        const app011CloseAllert = () => {
          setApp011setMsg("")
        }      

//Page01
const [app011p01TabelSearch, setApp011p01TabelSearch] = useState('');

const [app011p01TabelDivisiReq, setApp011p01TabelDivisiReq] = useState({ page: 1, limit: 10, offset: 0, sort: "divisiCd", order: "asc", search: { any: app011p01TabelSearch } });

const app011p01hendleTableChange = (type, { page, sortField, sortOrder, sizePerPage }) => {
  if (type === "sort") {
    setApp011p01TabelDivisiReq({ page: 1, limit: sizePerPage, offset: 0, sort: sortField, order: sortOrder, search: { any: app011p01TabelSearch } });
  }
  if (type === "pagination") {
    setApp011p01TabelDivisiReq({ page: page, limit: sizePerPage, offset: ((page - 1) * sizePerPage), sort: sortField, order: sortOrder, search: { any: app011p01TabelSearch } });
  }
};

useEffect(() => {
  setApp011p01firstRenderDone(true);
}, []);

useEffect(() => {
  if (app011p01firstRenderDone) {
    dispatch(getDivisiData(app011p01TabelDivisiReq))
  }
}, [app011p01TabelDivisiReq])

useEffect(() => {
  setApp011p01firstRenderDone(true);
  dispatch(getCombo({"name":"combo-gis-area-type"}))
}, [])

const {
  app011SelectedGisType
} = useSelector(state => ({
  app011SelectedGisType: state.ComboReducer.respGysAreaType.data != null ? state.ComboReducer.respGysAreaType.data : []
}));

useEffect(() => {
  setApp011p01TabelDivisiReq({ page: 1, limit: 10, offset: 0, sort: "divisiCd", order: "asc", search: { any: app011p01TabelSearch } });
}, [app011p01TabelSearch])

const app011p01DivisiData  = useSelector(state => {
  return state.DivisiReducer.respGetDivisi;
});

useEffect(() => {
    if(app011p01DivisiData.status == "0"){
      setApp011setMsg(app011p01DivisiData)
    }
  }, [app011p01DivisiData])

  useEffect(() => {
    if(app011p01firstRenderDone){
      dispatch(getDivisiData(app011p01TabelDivisiReq))
    }
  }, [app011p01TabelDivisiReq])
  
  const app011p01TabelDivisiColumns = [
    {
      dataField: "divisiId",
      text: "Divisi ID",
      sort: true,
      hidden: true,
      headerStyle: {textAlign: 'center'},
    },
    {
        dataField: "divisiCd",
        text: "Divisi Code",
        sort: true,
        headerStyle: {textAlign: 'center'},
    },
    {
        dataField: "divisiDescnm",
        text: "Divisi Name",
        sort: true,
        headerStyle: {textAlign: 'center'},
    },
    {
        dataField: "plantCd",
        text: "Plant Code",
        sort: true,
        headerStyle: {textAlign: 'center'},
    },
    {
        dataField: "plantNm",
        text: "Plant Name",
        sort: true,
        headerStyle: {textAlign: 'center'},
    },
    {
      dataField: "action",
      isDummyField: true,
      text: "Action",
      headerStyle: {textAlign: 'center'},
      formatter: (cellContent, app011p01DivisiData) => (
        <>
          <div style={{justifyContent : 'center'}} className="d-flex gap-3">
              <i className="mdi mdi-pencil font-size-18  text-primary" id="edittooltip" onClick={() => app011p01PreEdit(app011p01DivisiData)}/>
              <UncontrolledTooltip placement="top" target="edittooltip">
                Edit
              </UncontrolledTooltip>

              <i className="mdi mdi-delete font-size-18 text-danger" id="deletetooltip" onClick={() => app011p01Delete(app011p01DivisiData)} />
              <UncontrolledTooltip placement="top" target="deletetooltip">
                Delete
              </UncontrolledTooltip>

          </div>
        </>
      ),
    },
  ]
  
  const app011p01PreAdd = () => {
    app011p02ValidInput.setFieldValue("divisiId", "")
    app011p02ValidInput.setFieldValue("divisiCd", "")
    app011p02ValidInput.setFieldValue("divisiDescnm", "")
    app011p02ValidInput.setFieldValue("plantCd", "")
    app011p02ValidInput.setFieldValue("plantNm", "")
    app011p02ValidInput.setFieldValue("gisAreajson", "")
    app011p02ValidInput.setFieldValue("gisAreatype", "")
    app011p02ValidInput.setFieldValue("divisiIntegrityCode", "")
    setApp011p02LovPlant("")
    setApp011setMsg("")
    setApp011p01Page(false)
    setApp011p02Page(true)
  }
  const app011p01PreEdit = (app011p01DivisiData) => {
    setApp011setMsg("")
    console.log(app011p01DivisiData);
    app011p03ValidInput.setFieldValue("divisiId", app011p01DivisiData.divisiId)
    app011p03ValidInput.setFieldValue("plantCd", app011p01DivisiData.plantCd)
    app011p03ValidInput.setFieldValue("divisiCd", app011p01DivisiData.divisiCd)
    app011p03ValidInput.setFieldValue("divisiDescnm", app011p01DivisiData.divisiDescnm)
    app011p02ValidInput.setFieldValue("plantNm", app011p01DivisiData.plantNm)
    app011p03ValidInput.setFieldValue("gisAreajson", app011p01DivisiData.gisAreajson)
    app011p03ValidInput.setFieldValue("gisAreatype", app011p01DivisiData.gisAreatype?.toString())
    app011p03ValidInput.setFieldValue("divisiIntegrityCode", app011p01DivisiData.divisiIntegrityCode)
    setApp011p01Page(false)
    setApp011p03Page(true)
  }

  const [app011p01DivisiDelete, setApp011p01DivisiDelete] = useState(null);

  const app011p01Delete = (app011p01DivisiData) => {
    setApp011setMsg("")
    setApp011p01DivisiDelete(app011p01DivisiData);
    setApp011DeleteModal(true)
  }

  const  app011HandleDeleteDivisi = () => {
    if (app011p01DivisiDelete.divisiId) {
      dispatch(deleteDivisi(app011p01DivisiDelete));
      setApp011DeleteModal(false);
      setApp011p01DivisiDelete(null)
    }
  }

  const app011p01Message  = useSelector(state => {
    return state.DivisiReducer.msgDelete;
  });

  useEffect(() => {
    if(app011p01Message.status == "1"){
      dispatch(getDivisiData(app011p01TabelDivisiReq))
    }
    setApp011setMsg(app011p01Message);
  }, [app011p01Message])

  //p02/Add//

  const [app011p02Spinner, setApp011p02Spinner] = useState(false);
  const [app011p02LovPlant, setApp011p02LovPlant] = useState('');
  const app011p02ValidInput = useFormik({
    enableReinitialize: true,

    initialValues: {
    plantId: '',
    plantCd: '',
    divisiCd: '',
	  divisiDescnm: '',
    plantNm: '',
    gisAreajson: '',
    gisAreatype: '',
    divisiIntegrityCode: '',
    },
    validationSchema: Yup.object().shape({
     
      divisiCd: Yup.string()
        .required("Wajib diisi"),
      divisiDescnm: Yup.string()
        .required("Wajib diisi"),
      plantCd: Yup.string()
        .required("Wajib diisi"),
    }),
    onSubmit: (values) => {
      setApp011p02Spinner(true);
      setApp011setMsg("")
      dispatch(saveDivisi(values));
    }
  });

  const app011p02Message  = useSelector(state => {
    return state.DivisiReducer.msgAdd;
  });

  useEffect(() => {
    if(app011p02Message.status == "1"){
     
      setApp011p01Page(true); 
      setApp011p02Page(false);
      dispatch(getDivisiData(app011p01TabelDivisiReq))
    }
    setApp011setMsg(app011p02Message)
    setApp011p02Spinner(false);
  }, [app011p02Message])

  const app011p02TabelLovPlantColumns = [
    {
      dataField: "plantCd",
      text: "Plant CD",
      sort: true,
      headerStyle: { textAlign: 'center' },
    },
    {
      dataField: "plantNm",
      text: "Plant Name",
      sort: true,
      headerStyle: { textAlign: 'center' },
    },
  ]

  //p03/Edit//

  const [app011p03Spinner, setapp011p03Spinner] = useState(false);
  const app011p03ValidInput = useFormik({
    enableReinitialize: true,
  
    initialValues: {
      plantId: '',
      plantCd: '',
      divisiCd: '',
      divisiDescnm: '',
      gisAreajson: '',
      gisAreatype: '',
      divisiIntegrityCode: '',
    },
    validationSchema: Yup.object().shape({
    divisiDescnm: Yup.string()
      .required("Wajib diisi"),
    }),

    
    onSubmit: (values) => {
      setapp011p03Spinner(true);
      setApp011setMsg("")
      dispatch(editDivisi(values));
    }
  });

  const app011p03Message  = useSelector(state => {
    return state.DivisiReducer.msgEdit;
  });

  useEffect(() => {
    if(app011p03Message.status == "1"){
      setApp011p01Page(true); 
      setApp011p03Page(false);
      dispatch(getDivisiData(app011p01TabelDivisiReq))
    }
    setApp011setMsg(app011p03Message)
    setapp011p03Spinner(false);
  }, [app011p03Message])



    return (
      <React.Fragment>
        <div className="page-content">
        <MetaTags>
          <title>
          Korindo App
          </title>
        </MetaTags>

        <DeleteModal
          show={app011DeleteModal}
          onDeleteClick={app011HandleDeleteDivisi}
          onCloseClick={() => setApp011DeleteModal(false)}
        />

        {app011Msg !== ""? <UncontrolledAlert toggle={app011CloseAllert} color={app011Msg.status == "1" ? "success" : "danger"}>
        {typeof app011Msg == 'string' ? app011Msg : app011Msg.listmessage?.map((msg, key) => ( <p key={key}>{"* " +msg}</p>))}</UncontrolledAlert> : null}


      <Container style={{display : app011p01Page ? 'block' : 'none'}} fluid={true}>
          <Breadcrumbs title="Forms" breadcrumbItem="Master Divisi" />
      <Row>
      <Col>
      <Card>
        <CardHeader><i className="bx bx-list-check font-size-18 align-middle me-2"></i>List Divisi</CardHeader>
          <CardBody>
          <React.Fragment>
                        <Row className="mb-2">
                              <Col sm="2">
                                <div className="form-group m-0">
                                  <div className="input-group">
                                    <input
                                      type="text"
                                      className="form-control"
                                      placeholder="Search ..."
                                      value={app011p01TabelSearch}
                                      onChange={  e  => { setApp011p01TabelSearch(e.target.value); }} 
                                    />
                                    <div className="input-group-append">
                                      <button className="btn btn-dark" type="button" 
                                      onClick={() => {
                                        dispatch(getDivisiData(app011p01TabelDivisiReq))
                                    }}>
                                        <i className="mdi mdi-magnify" />
                                      </button>
                                    </div>
                                  </div>
                                </div>
                              </Col>
                              <Col sm="10">
                                <div className="text-sm-end">
                                <button
                                  type="button"
                                  className="btn btn-primary "
                                 onClick= {() =>  { app011p01PreAdd()}}
                                >
                                  <i className="bx bx-plus font-size-16 align-middle me-2"></i>{" "}
                                  Add
                                </button>
                                </div>
                              </Col>
                      </Row>
                      <Row>
                      <BootstrapTable
                        wrapperClasses="table-responsive"
                        keyField = "divisiId"
                        rowClasses="text-nowrap"
                        remote ={{ filter: true, pagination: true, sort: true, cellEdit: true }}
                        data = {app011p01DivisiData.data != null ? app011p01DivisiData.data.divisi : []}
                        columns = {app011p01TabelDivisiColumns}
                        pagination = {paginationFactory ({
                            page : app011p01TabelDivisiReq.page,
                            sizePerPage : app011p01TabelDivisiReq.limit,
                            sizePerPageList : [5, 10,20],
                            totalSize : app011p01DivisiData.data != null ? app011p01DivisiData.data.divisitotal : 0,
                            showTotal: true,
                        })}
                        classes={
                          "table align-middle table-nowrap"
                        }
                        onTableChange = {app011p01hendleTableChange}
                        striped
                        hover
                        condensed
                      />
                      </Row>
          </React.Fragment>
          </CardBody>
        </Card>
        </Col>
     </Row>
      </Container>

      {/* //p02 */}
      <Container style={{display : app011p02Page ? 'block' : 'none'}} fluid={true}>
              <Breadcrumbs title="Forms" breadcrumbItem="Add Divisi" />
            
              <Row>
                <Col lg={12}>
                  <Card>
                    <CardHeader><i className="bx bx-add-to-queue font-size-18 align-middle me-2"></i>Add Divisi</CardHeader>
                    <CardBody>
                      <Form
                        onSubmit={(e) => {
                          e.preventDefault();
                          app011p02ValidInput.handleSubmit();
                          return false;
                        }}>
                      <FormGroup className="mb-0">
                            <Row>
                              <Col md="5">
                              <div>
                                <Lov title="Plant Code"
                                  LenghtLabel=""
                                  LenghtDiv="mb-3 col-sm-10"
                                  columns={app011p02TabelLovPlantColumns}
                                  getData={getPlant}
                                  pageSize={10}
                                  defaultSetInput="plantCd"
                                  invalidData={app011p02ValidInput}
                                  fieldValue="plantCd"
                                  stateSearchInput={app011p02LovPlant}
                                  stateSearchInputSet={setApp011p02LovPlant}
                                  touchedLovField={app011p02ValidInput.touched.plantCd}
                                  errorLovField={app011p02ValidInput.errors.plantCd}
                                />
                              </div> 

                              <div className="mb-3 col-sm-10">
                                    <Label>Divisi Code</Label>
                                    <Input
                                      name="divisiCd"
                                      type="text"
                                      maxLength={5}
                                      onChange={app011p02ValidInput.handleChange}
                                      value={app011p02ValidInput.values.divisiCd || ""}
                                      invalid={
                                        app011p02ValidInput.touched.divisiCd && app011p02ValidInput.errors.divisiCd ? true : false
                                      }
                                    />
                                    {app011p02ValidInput.touched.divisiCd && app011p02ValidInput.errors.divisiCd ? (
                                      <FormFeedback type="invalid">{app011p02ValidInput.errors.divisiCd}</FormFeedback>
                                    ) : null}
                              </div>

                              <div className="mb-3 col-sm-10">
                                    <Label>Divisi Name</Label>
                                    <Input
                                      name="divisiDescnm"
                                      type="text"
                                      maxLength={45}
                                      onChange={app011p02ValidInput.handleChange}
                                      value={app011p02ValidInput.values.divisiDescnm || ""}
                                      invalid={
                                        app011p02ValidInput.touched.divisiDescnm && app011p02ValidInput.errors.divisiDescnm ? true : false
                                      }
                                    />
                                    {app011p02ValidInput.touched.divisiDescnm && app011p02ValidInput.errors.divisiDescnm ? (
                                      <FormFeedback type="invalid">{app011p02ValidInput.errors.divisiDescnm}</FormFeedback>
                                    ) : null}
                              </div>
                              </Col>

                              <Col md="5">
                              <div className="mb-3 col-sm-10">
                                    <Label>GYS Area</Label>
                                    <Input
                                      name="gisAreajson"
                                      type="textarea"
                                  
                                      onChange={app011p02ValidInput.handleChange}
                                      value={app011p02ValidInput.values.gisAreajson || ""}
                                      invalid={
                                        app011p02ValidInput.touched.gisAreajson && app011p02ValidInput.errors.gisAreajson ? true : false
                                      }
                                      
                                    />
                                    {app011p02ValidInput.touched.gisAreajson && app011p02ValidInput.errors.gisAreajson ? (
                                      <FormFeedback type="invalid">{app011p02ValidInput.errors.gisAreajson}</FormFeedback>
                                    ) : null}
                              </div>
                                    
                              <div className="mb-3 col-sm-10">
                                    <Label>GYS Area Type</Label>
                                    <Input
                                      type="select"
                                      name="gisAreatype"
                                      onChange= {app011p02ValidInput.handleChange}
                                      onBlur={app011p02ValidInput.handleBlur}
                                      value={app011p02ValidInput.values.gisAreatype || ""}
                                      invalid={
                                        app011p02ValidInput.touched.gisAreatype && app011p02ValidInput.errors.gisAreatype ? true : false
                                      }
                                    >
                                      <option></option>
                                      {
                                        app011SelectedGisType.dtlsetting?.map((value, key) =>
                                        <option key={key} value={value.id}>{value.desc}</option>) 
                                      }
                                    </Input>                      
                                    {app011p02ValidInput.touched.gisAreatype && app011p02ValidInput.errors.gisAreatype ? (
                                      <FormFeedback type="invalid">{app011p02ValidInput.errors.gisAreatype}</FormFeedback>
                                    ) : null}
                              </div>

                              <div className="mb-3 col-sm-10">
                                    <Label>Integrity Code</Label>
                                    <Input
                                      name="divisiIntegrityCode"
                                      type="text"
                                      maxLength={50}
                                      onChange={app011p02ValidInput.handleChange}
                                      value={app011p02ValidInput.values.divisiIntegrityCode || ""}
                                      invalid={
                                        app011p02ValidInput.touched.divisiIntegrityCode && app011p02ValidInput.errors.divisiIntegrityCode ? true : false
                                      }
                                    />
                                    {app011p02ValidInput.touched.divisiIntegrityCode && app011p02ValidInput.errors.divisiIntegrityCode ? (
                                      <FormFeedback type="invalid">{app011p02ValidInput.errors.divisiIntegrityCode}</FormFeedback>
                                    ) : null}
                              </div>
                              </Col>
                            </Row>
                          <Button type="submit" color="primary" className="ms-1">
                              <i className="bx bxs-save align-middle me-2"></i>{" "}
                                Submit
                          </Button>
                          <Spinner style={{ display: app011p02Spinner ? "block" : "none", marginTop : '-35px'}} className="ms-4" color="danger" />
                        </FormGroup>
                        
                      </Form>
                            
                    </CardBody>
                  </Card>
                </Col>
              </Row>
              <div className="text-sm-end">
                <button
                  type="button"
                  className="btn btn-danger "
                  onClick ={() => { setApp011p01Page(true); setApp011p02Page(false); setApp011setMsg("")}}
                  >
                  <i className="bx bx-arrow-back align-middle me-2"></i>{" "}
                  Back
                </button>
              </div>            
      </Container>

      {/* //P03 */}
      <Container style={{ display: app011p03Page ? 'block' : 'none' }} fluid={true}>
      <Breadcrumbs title="Forms" breadcrumbItem="Maintain Divisi" />
          <Row>
      
            <Col lg={12}>
              <Card>
                <CardHeader><i className="bx bxs-edit-alt font-size-18 align-middle me-2"></i>Edit Divisi</CardHeader>
                <CardBody>
                  <Form
                    onSubmit={(e) => {
                      e.preventDefault();
                      app011p03ValidInput.handleSubmit();
                      return false;
                    }}>
                    <FormGroup className="mb-0">
                      <Row>
                          <Col md="5">
                              <div className="mb-3 col-sm-10">
                            <Label>Plant Code</Label>
                            <Input
                              name="plantCd"
                              type="text"
                              maxLength={60}
                              disabled
                              onChange={app011p03ValidInput.handleChange}
                              value={app011p03ValidInput.values.plantCd || ""}
                            />
                              </div>

                              <div className="mb-3 col-sm-10">
                                <Label>Divisi Code</Label>
                                <Input
                                  name="divisiCd"
                                  type="text"
                                  maxLength={5}
                                  disabled
                                  onChange={app011p03ValidInput.handleChange}
                                  value={app011p03ValidInput.values.divisiCd || ""}
                                />
                              </div>

                              <div className="mb-3 col-sm-10">
                                    <Label>Divisi Name</Label>
                                    <Input
                                      name="divisiDescnm"
                                      type="text"
                                      maxLength={45}
                                      onChange={app011p03ValidInput.handleChange}
                                      value={app011p03ValidInput.values.divisiDescnm || ""}
                                      invalid={
                                        app011p03ValidInput.touched.divisiDescnm && app011p03ValidInput.errors.divisiDescnm ? true : false
                                      }
                                    />
                                    {app011p03ValidInput.touched.divisiDescnm && app011p03ValidInput.errors.divisiDescnm ? (
                                      <FormFeedback type="invalid">{app011p03ValidInput.errors.divisiDescnm}</FormFeedback>
                                    ) : null}
                              </div>
                          </Col>

                          <Col md="5">
                          <div className="mb-3 col-sm-10">
                                    <Label>GIS Area</Label>
                                    <Input
                                      name="gisAreajson"
                                      type="textarea"
                                      maxLength={45}
                                      onChange={app011p03ValidInput.handleChange}
                                      value={app011p03ValidInput.values.gisAreajson || ""}
                                      invalid={
                                        app011p03ValidInput.touched.gisAreajson && app011p03ValidInput.errors.gisAreajson ? true : false
                                      }
                                    />
                                    {app011p03ValidInput.touched.gisAreajson && app011p03ValidInput.errors.gisAreajson ? (
                                      <FormFeedback type="invalid">{app011p03ValidInput.errors.gisAreajson}</FormFeedback>
                                    ) : null}
                              </div>
                                    
                              <div className="mb-3 col-sm-10">
                                    <Label>GIS Area Type</Label>
                                    <Input
                                      type="select"
                                      name="gisAreatype"
                                      onChange= {app011p02ValidInput.handleChange}
                                      onBlur={app011p02ValidInput.handleBlur}
                                      value={app011p02ValidInput.values.gisAreatype || ""}
                                      invalid={
                                        app011p02ValidInput.touched.gisAreatype && app011p02ValidInput.errors.gisAreatype ? true : false
                                      }
                                    >
                                      <option></option>
                                      {
                                        app011SelectedGisType.dtlsetting?.map((value, key) =>
                                        <option key={key} value={value.id}>{value.desc}</option>) 
                                      }
                                    </Input>                      
                                    {app011p02ValidInput.touched.gisAreatype && app011p02ValidInput.errors.gisAreatype ? (
                                      <FormFeedback type="invalid">{app011p02ValidInput.errors.gisAreatype}</FormFeedback>
                                    ) : null}
                              </div>

                              <div className="mb-3 col-sm-10">
                                    <Label>Integrity Code</Label>
                                    <Input
                                      name="divisiIntegrityCode"
                                      type="text"
                                      maxLength={45}
                                      onChange={app011p03ValidInput.handleChange}
                                      value={app011p03ValidInput.values.divisiIntegrityCode || ""}
                                      invalid={
                                        app011p03ValidInput.touched.divisiIntegrityCode && app011p03ValidInput.errors.divisiIntegrityCode ? true : false
                                      }
                                    />
                                    {app011p03ValidInput.touched.divisiIntegrityCode && app011p03ValidInput.errors.divisiIntegrityCode ? (
                                      <FormFeedback type="invalid">{app011p03ValidInput.errors.divisiIntegrityCode}</FormFeedback>
                                    ) : null}
                              </div>
                          </Col>
                          </Row>
                          <Button type="submit" color="primary" className="ms-1">
                              <i className="bx bxs-save align-middle me-2"></i>{" "}
                                Submit
                          </Button>
                          <Spinner style={{ display: app011p03Spinner ? "block" : "none", marginTop : '-35px'}} className="ms-4" color="danger" />
                    </FormGroup>   
                  </Form>
                          
                </CardBody>
              </Card>
            </Col> 
          </Row>   
          <div className="text-sm-end">
                <button
                  type="button"
                  className="btn btn-danger "
                  onClick ={() => { setApp011p01Page(true); setApp011p03Page(false); setApp011setMsg("")}}
                  >
                  <i className="bx bx-arrow-back align-middle me-2"></i>{" "}
                  Back
                </button>
              </div>           
      </Container>
  </div>
  </React.Fragment>
 );
};
    export default Divisi;