import {

        GET_DIVISI,
        RESP_DIVISI,
        EDIT_DIVISI,
        SAVE_DIVISI,
        DELETE_DIVISI,
        MSGADD,
        MSGEDIT,
        MSGDELETE,
        RESET_MESSAGE,

}from "./actionTypes"

const INIT_STATE = {

    respGetDivisi: {},
    msgEdit: "",
    msgAdd: "",
    msgDelete: "",

  }

  const divisiReduce = (state = INIT_STATE, action) => {

    switch (action.type) {
        case GET_DIVISI:
          return {
            ...state,
          }
        case RESP_DIVISI:
          return {
            ...state,
            respGetDivisi: action.payload,
          }
          case EDIT_DIVISI:
          return {
            ...state,
          }
        case SAVE_DIVISI:
          return {
            ...state,
        }
        case DELETE_DIVISI:
          return {
            ...state,
        }
        case MSGEDIT:
          return {
            ...state,
            msgEdit: action.payload,
          }
        case MSGDELETE:
            return {
              ...state,
              msgDelete: action.payload,
        }
        case MSGADD:
          return {
            ...state,
            msgAdd: action.payload,
        }
        case RESET_MESSAGE :
            return {
               ...state,
               msgEdit: "",
               msgAdd: "",
               msgDelete: "",
            }
        
        default:
          return state
      }

  } 
  export default divisiReduce