import {
    GET_TEAM,
    RESP_GET_TEAM,
    GET_TEAM_CHILD,
    RESP_GET_TEAM_CHILD,
  } from "./actionTypes"

  export const getteamData = (req) => ({
    type: GET_TEAM,
    payload: req,
  })
  
  export const respGetteam = resp => ({
    type: RESP_GET_TEAM,
    payload: resp,
  })

  export const getteamChildData = (req) => ({
    type: GET_TEAM_CHILD,
    payload: req,
  })
  
  export const respGetteamChild = resp => ({
    type: RESP_GET_TEAM_CHILD,
    payload: resp,
  })

  