import { call, put, takeEvery} from "redux-saga/effects"

import { GET_MPLANT,EDIT_PLANT,SAVE_PLANT,DELETE_PLANT  } from "./actionTypes"
import { respGetMp,msgEdit, msgAdd, msgDelete } from "./actions"

import { getMasterPlant,editPlant,savePlant,deletePlant } from "helpers/backend_helper"

function* fetchGetMPlant({ payload: req }) {
  try {
    const response = yield call(getMasterPlant, req)
    console.log("RESPONSE :",response);
    if(response.status == 1){
      yield put(respGetMp(response))
    }else{
      yield put(respGetMp(response))
    }
  } catch (error) {
    console.log(error);
    yield put(respGetMp({"status" : 0, "message" : "Error Get Data"}))
  }
}

function* fetchEditPlant({ payload: req }) {
  try {
    const response = yield call(editPlant, req)
    yield put(msgEdit(response))
  } catch (error) {
    console.log(error);
    yield put(msgEdit({"status" : 0, "message" : "Error Get Data"}))
  }
}

function* fetchSavePlant({ payload: req }) {
  try {
    const response = yield call(savePlant, req)
    yield put(msgAdd(response))
  } catch (error) {
    console.log(error);
    yield put(msgAdd({"status" : 0, "message" : "Error Get Data"}))
  }
}

function* fetchDeletePlant({ payload: req }) {
  try {
    const response = yield call(deletePlant, req)
    yield put(msgDelete(response))
  } catch (error) {
    console.log(error);
    yield put(msgDelete({"status" : 0, "data" : "Error Delete Data"}))
  }
}



function* mstPlantSaga() {
    
  yield takeEvery(GET_MPLANT, fetchGetMPlant)
  yield takeEvery(EDIT_PLANT, fetchEditPlant)
  yield takeEvery(SAVE_PLANT, fetchSavePlant)
  yield takeEvery(DELETE_PLANT, fetchDeletePlant)

}

export default mstPlantSaga
