import {
    GET_COMP,
    RESP_COMP,
    MSGEDIT,
    MSGDELETE,
    MSGADD,
    RESET_MESSAGE,
    EDIT_COMP,
    SAVE_COMP,
    DELETE_COMP,
  } from "./actionTypes"

  export const getCompanyData = (req) => ({
    type: GET_COMP,
    payload: req,
  })
  
  export const respGetCompany = resp => ({
    type: RESP_COMP,
    payload: resp,
  })

  export const msgEdit = resp => ({
    type: MSGEDIT,
    payload: resp,
  })

  export const msgAdd = resp => ({
    type: MSGADD,
    payload: resp,
  })

  export const msgDelete = resp => ({
    type: MSGDELETE,
    payload: resp,
  })

  export const resetMessage = (resp) => ({
    type: RESET_MESSAGE,
    payload: resp,
  })

  export const editCompany = (req) => ({
    type: EDIT_COMP,
    payload: req,
  })

  export const saveCompany = (req) => ({
    type: SAVE_COMP,
    payload: req,
  })

  export const deleteCompany = (req) => ({
    type: DELETE_COMP,
    payload: req,
  })
  

  
  