import React, { useState, useEffect } from "react";
import MetaTags from "react-meta-tags";  
import Breadcrumbs from "../../components/Common/Breadcrumb";
import BootstrapTable from "react-bootstrap-table-next";

import DeleteModal from "../../common/DeleteModal";
import paginationFactory from "react-bootstrap-table2-paginator";

import { useFormik, } from "formik";
import * as Yup from "yup";
import { geMpData,editPlant,resetMessage,deletePlant,savePlant } from "../../store/app006/actions"
import Lov from "../../common/Lov";
import { getCompany } from "../../store/lov/actions"

import {
  Row,
  Col,
  Card,
  CardBody,
  Container,
  Button,
  UncontrolledTooltip,
  Label,
  Input,
  FormFeedback,
  Form,
  Spinner,
  FormGroup,
  CardHeader,
  UncontrolledAlert,
  Nav,
  NavItem,
  NavLink,
  TabContent,
  TabPane,
  CardText,
} from "reactstrap";


 import { useSelector, useDispatch } from "react-redux"

const MasterPlant = () => {

  const dispatch = useDispatch();

  const [app006p01firstRenderDone, setApp006p01firstRenderDone] = useState(false);
  const [app006p01Page, setApp006p01Page] = useState(true)
  const [app006p02Page, setApp006p02Page] = useState(false)
  const [app006p03Page, setApp006p03Page] = useState(false)
  const [app006Msg, setApp006setMsg] = useState("")
  const [app006DeleteModal, setApp006DeleteModal] = useState(false);

  
  useEffect(() => {
    dispatch(resetMessage());
    
  }, [dispatch])

  const app006CloseAllert = () => {
    setApp006setMsg("")
  }

   //p01
   const [app006p01TabelSearch, setApp006p01TabelSearch] = useState('');
   const [app006p01TabelMasterPlantReq, setApp006p01TabelMasterPlantReq] = useState({page : 1, limit: 10, offset: 0, sort: "plantCd", order: "asc", search: {any : app006p01TabelSearch}});
  
   const app006p01hendleTableChange = (type, {page, sortField, sortOrder, sizePerPage}) => {   
    if(type === "sort"){
      setApp006p01TabelMasterPlantReq({page : 1, limit: sizePerPage, offset: 0, sort: sortField, order: sortOrder, search: {any : app006p01TabelSearch}});
    }
    if(type==="pagination"){
      setApp006p01TabelMasterPlantReq({page : page, limit: sizePerPage, offset: ((page-1) * sizePerPage), sort: sortField, order: sortOrder, search: {any : app006p01TabelSearch}});
    }
  };


   useEffect(() => {
    setApp006p01firstRenderDone(true);
  }, []);

  useEffect(() => {
    if(app006p01firstRenderDone){
      dispatch(geMpData(app006p01TabelMasterPlantReq))
    }
  }, [app006p01TabelMasterPlantReq])

  
  useEffect(() => {
    setApp006p01TabelMasterPlantReq({page : 1, limit: 10, offset: 0, sort: "plantCd", order: "asc", search: {any : app006p01TabelSearch}});
  }, [app006p01TabelSearch])

  const app006p01MasterPlantData  = useSelector(state => {
    return state.MasterPlantReducer.respGetmPlant;
      
  });

  useEffect(() => {
    if(app006p01MasterPlantData.status == "0"){
      setApp006setMsg(app006p01MasterPlantData)
    }
  }, [app006p01MasterPlantData])


  useEffect(() => {
    if(app006p01firstRenderDone){
      dispatch(geMpData(app006p01TabelMasterPlantReq))
    }
  }, [app006p01TabelMasterPlantReq])

  const app006p01TabelMasterPlantColumns = [
    {
      dataField: "plantCd",
      text: "Plant Code",
      sort: true,
      headerStyle: { textAlign: 'center' },
    },
    {
      dataField: "plantNm",
      text: "Plant Name",
      sort: true,
      headerStyle: { textAlign: 'center' },
    },
    {
      dataField: "companyCd",
      text: "Company Code",
      sort: true,
      headerStyle: { textAlign: 'center' },
    },
    {
      dataField: "companyNm",
      text: "Company Name",
      sort: true,
      headerStyle: { textAlign: 'center' },
    },
    {
      dataField: "action",
      isDummyField: true,
      text: "Action",
      headerStyle: { textAlign: 'center' },
      formatter: (cellContent, app006p01MasterPlantData) => (
        <>
          <div style={{ justifyContent: 'center' }} className="d-flex gap-3">
            <i className="mdi mdi-pencil font-size-18  text-primary" id="edittooltip" onClick={() => app006p01PreEdit(app006p01MasterPlantData)} />
            <UncontrolledTooltip placement="top" target="edittooltip">
              Edit
            </UncontrolledTooltip>

            <i className="mdi mdi-delete font-size-18 text-danger" id="deletetooltip" onClick={() => app006p01Delete(app006p01MasterPlantData)} />
            <UncontrolledTooltip placement="top" target="deletetooltip">
              Delete
            </UncontrolledTooltip>

          </div>
        </>
      ),
    },
  ]

  const app006p01PreAdd = () => {
    app006p02ValidInput.setFieldValue("plantCd", "")
    app006p02ValidInput.setFieldValue("plantNm", "")
    app006p02ValidInput.setFieldValue("companyCd", "")
    app006p02ValidInput.setFieldValue("companyNm", "")
    setApp006p02LovCompany("")
    setApp006setMsg("")
    setApp006p01Page(false)
    setApp006p02Page(true)
  }

  const app006p01PreEdit = (app006p01MasterPlantData) => {
    setApp006setMsg("")
    app006p03ValidInput.setFieldValue( "plantCd", app006p01MasterPlantData.plantCd)
    app006p03ValidInput.setFieldValue( "plantNm", app006p01MasterPlantData.plantNm)
    setApp006p03LovCompany(app006p01MasterPlantData.companyCd);
    setApp006p01Page(false)
    setApp006p03Page(true)
  }

  const [app006p01MasterPlantDelete, setApp006p01MasterPlantDelete] = useState(null);

  const app006p01Delete = (app006p01MasterPlantData) => {
    setApp006setMsg("")
    setApp006p01MasterPlantDelete(app006p01MasterPlantData);
    setApp006DeleteModal(true)
  }

  const  app006HandleDeleteMenu = () => {
    if (app006p01MasterPlantDelete.plantCd) {
      dispatch(deletePlant(app006p01MasterPlantDelete));
      setApp006DeleteModal(false);
      setApp006p01MasterPlantDelete(null)
    }
  }

  const app006p01Message  = useSelector(state => {
    return state.MasterPlantReducer.msgDelete; 
  });

  useEffect(() => {
    if(app006p01Message.status == "1"){
      dispatch(geMpData(app006p01TabelMasterPlantReq))
    }
    setApp006setMsg(app006p01Message);
  }, [app006p01Message])

//p02
const [app006p02Spinner, setApp006p02Spinner] = useState(false);

const app006p02ValidInput = useFormik({
  enableReinitialize: true,

  initialValues: {
    plantCd: '',
    plantNm: '', 
    companyCd: '',
  },
  validationSchema: Yup.object().shape({
    plantCd: Yup.string().required("Wajib diisi"),
    plantNm: Yup.string().required("Wajib diisi"),
     companyCd: Yup.string().required("Wajib diisi"),
  }),


  onSubmit: (values) => {
    setApp006p02Spinner(true);
    setApp006setMsg("")
    dispatch(savePlant(values));
  }
});

const app006p02Message = useSelector(state => {
  return state.MasterPlantReducer.msgAdd;
});

useEffect(() => {
  if (app006p02Message.status == "1") {
    dispatch(geMpData(app006p01TabelMasterPlantReq))
    setApp006p01Page(true);
    setApp006p02Page(false);
  }
  setApp006setMsg(app006p02Message)
  setApp006p02Spinner(false);
}, [app006p02Message])

const [app006p02LovCompany, setApp006p02LovCompany] = useState('');

const app006p02LovCompanyColumns = [
  {
    dataField: "companyCd",
    text: "Company Code",
    sort: true,
    headerStyle: { textAlign: 'center' },
  },
  {
    dataField: "companyNm",
    text: "Company Name",
    sort: true,
    headerStyle: { textAlign: 'center' },
  },
]

 //p03
 const [app006p03Spinner, setApp006p03Spinner] = useState(false);
 const app006p03ValidInput = useFormik({
   enableReinitialize: true,
 
   initialValues: {
     plantcd: '',
     plantNm : '',
     companyCd : '',
   },
   validationSchema: Yup.object().shape({
     plantCd: Yup.string().required("Wajib diisi"),
     plantNm: Yup.string().required("Wajib diisi"),
     companyCd: Yup.string().required("Wajib diisi"),
   }),

   
   onSubmit: (values) => {
     setApp006p03Spinner(true);
     setApp006setMsg("")
     dispatch(editPlant(values));
   }
 });

 const app006p03Message  = useSelector(state => {
   return state.MasterPlantReducer.msgEdit;
 });

 useEffect(() => {
   if(app006p03Message.status == "1"){
     setApp006p01Page(true); 
     setApp006p03Page(false);
     dispatch(geMpData(app006p01TabelMasterPlantReq))
   }
   setApp006setMsg(app006p03Message)
   setApp006p03Spinner(false);
 }, [app006p03Message])

 const [app006p03LovCompany, setApp006p03LovCompany] = useState('');

 const app006p03LovCompanyColumns = [
  {
    dataField: "companyCd",
    text: "Company Code",
    sort: true,
    headerStyle: { textAlign: 'center' },
  },
  {
    dataField: "companyNm",
    text: "Company Name",
    sort: true,
    headerStyle: { textAlign: 'center' },
  },
]

  return (
    <React.Fragment>
      
      <div className="page-content">
        <MetaTags>
          <title>
          Korindo App
          </title>
        </MetaTags>

        <DeleteModal
          show={app006DeleteModal}
          onDeleteClick={app006HandleDeleteMenu}
          onCloseClick={() => setApp006DeleteModal(false)}
        />

        {app006Msg !== ""? <UncontrolledAlert toggle={app006CloseAllert} color={app006Msg.status == "1" ? "success" : "danger"}>
                  {typeof app006Msg == 'string' ? app006Msg : app006Msg.listmessage?.map((msg, key) => ( <p key={key}>{"* " +msg}</p>))}</UncontrolledAlert> : null}

        <Container style={{display : app006p01Page ? 'block' : 'none'}} fluid={true}>
          <Breadcrumbs title="Forms" breadcrumbItem="Master Plant" />
          
           <Row>
            <Col>
              <Card>
              <CardHeader><i className="bx bx-list-check font-size-18 align-middle me-2"></i>List Plant</CardHeader>
                 <CardBody>
                 <React.Fragment>
                             <Row className="mb-2">
                               <Col sm="2">
                                 <div className="form-group m-0">
                                   <div className="input-group">
                                   <input
                                       type="text"
                                       className="form-control"
                                       placeholder="Search ..."
                                       value={app006p01TabelSearch}
                                       onChange={ e  => {setApp006p01TabelSearch(e.target.value);} } 
                                     />
                                     <div className="input-group-append">
                                       <button className="btn btn-dark" type="button" 
                                          onClick={() => {
                                            dispatch(geMpData(app006p01TabelMasterPlantReq))
                                                         }}>
                                         <i className="mdi mdi-magnify" />
                                       </button>
                                     </div>
                                   </div>
                                 </div>
                               </Col>
                               <Col sm="10">
                                 <div className="text-sm-end">
                                 <button
                                       type="button"
                                       className="btn btn-primary "
                                        onClick= {() =>  { app006p01PreAdd()}}
                               >  
                                   <i className="bx bx-plus font-size-16 align-middle me-2"></i>{" "}
                                   Add 
                                 </button>
                                 </div>
                               </Col>
                             </Row>
                           <Row>
                            
                           <BootstrapTable
                              wrapperClasses="table-responsive"
                              keyField = "plantCd"
                              rowClasses="text-nowrap"
                              remote ={{ filter: true, pagination: true, sort: true, cellEdit: true }}
                              data = {app006p01MasterPlantData.data != null ? app006p01MasterPlantData.data.plant : []}
                              
                              columns = {app006p01TabelMasterPlantColumns}
                              pagination = {paginationFactory ({
                                  page : app006p01TabelMasterPlantReq.page,
                                  sizePerPage : app006p01TabelMasterPlantReq.limit,
                                  sizePerPageList : [5, 10,20],
                                  totalSize : app006p01MasterPlantData.data != null ? app006p01MasterPlantData.data.planttotal : 0,
                                  showTotal: true,
                              })}
                              classes={
                                "table align-middle table-nowrap"
                              }
                              onTableChange = {app006p01hendleTableChange}
                              striped
                              hover
                              condensed
                            />

                           </Row>
                  </React.Fragment>
                </CardBody>
              </Card>
            </Col>
          </Row>
        </Container>

{/* //p02 */}
        <Container style={{ display: app006p02Page ? 'block' : 'none' }} fluid={true}>
          <Breadcrumbs title="Forms" breadcrumbItem="Add Master Plant" />

          <Row>
            <Col lg={12}>
              <Card>
                <CardHeader><i className="bx bx-add-to-queue font-size-18 align-middle me-2"></i> Add Master Plant</CardHeader>
                <CardBody>
                  <Form
                    onSubmit={(e) => {
                      e.preventDefault();
                      app006p02ValidInput.handleSubmit();
                      return false;
                    }}>
                    <FormGroup className="mb-0">

                      <div className="mb-3 col-sm-3">
                        <Label>Plant Code</Label>
                        <Input
                          name="plantCd"
                          type="text"
                          maxLength={4}
                          onChange={app006p02ValidInput.handleChange}
                          value={app006p02ValidInput.values.plantCd || ""}
                          invalid={
                            app006p02ValidInput.touched.plantCd && app006p02ValidInput.errors.plantCd ? true : false
                          }
                        />
                        {app006p02ValidInput.touched.plantCd && app006p02ValidInput.errors.plantCd ? (
                          <FormFeedback type="invalid">{app006p02ValidInput.errors.plantCd}</FormFeedback>
                        ) : null}
                      </div>
                      <div className="mb-3 col-sm-3">
                        <Label>Plant Name</Label>
                        <Input
                          name="plantNm"
                          type="text"
                          maxLength={50}
                          onChange={app006p02ValidInput.handleChange}
                          value={app006p02ValidInput.values.plantNm || ""}
                          invalid={
                            app006p02ValidInput.touched.plantNm && app006p02ValidInput.errors.plantNm ? true : false
                          }
                        />
                        {app006p02ValidInput.touched.plantNm && app006p02ValidInput.errors.plantNm ? (
                          <FormFeedback type="invalid">{app006p02ValidInput.errors.plantNm}</FormFeedback>
                        ) : null}
                      </div>
                      <div>
                        <Lov title="Company Code"
                          LenghtLabel=""
                          LenghtDiv="mb-3 col-sm-3"
                          columns={app006p02LovCompanyColumns}
                          getData={getCompany}
                          pageSize={10}
                          // callbackFunc = {null}
                          // onChangeFunc = {null}
                          defaultSetInput="companyCd"
                          invalidData={app006p02ValidInput}
                          fieldValue="companyCd"
                          stateSearchInput={app006p02LovCompany}
                          stateSearchInputSet={setApp006p02LovCompany}
                          touchedLovField={app006p02ValidInput.touched.companyCd}
                          errorLovField={app006p02ValidInput.errors.companyCd}
                        />
                      </div>

                      <Button type="submit" color="primary" className="ms-1">
                        <i className="bx bxs-save align-middle me-2"></i>{" "}
                        Submit
                      </Button>
                      <Spinner style={{ display: app006p02Spinner ? "block" : "none", marginTop: '-35px' }} className="ms-4" color="danger" />
                    </FormGroup>

                  </Form>

                </CardBody>
              </Card>
            </Col>
          </Row>
          <div className="text-sm-end">
            <button
              type="button"
              className="btn btn-danger "
              onClick={() => { setApp006p01Page(true); setApp006p02Page(false); setApp006setMsg("") }}
            >
              <i className="bx bx-arrow-back align-middle me-2"></i>{" "}
              Back
            </button>
          </div>
        </Container>


        {/* //p03 */}
        <Container style={{display : app006p03Page ? 'block' : 'none'}} fluid={true}>
          <Breadcrumbs title="Forms" breadcrumbItem="Master Plant" />
                              
          <Row>
            <Col lg={12}>
              <Card>
                <CardHeader><i className="bx bxs-edit-alt font-size-18 align-middle me-2"></i>Edit Master Plant</CardHeader>
                <CardBody>
                  <Form
                    onSubmit={(e) => {
                      e.preventDefault();
                      app006p03ValidInput.handleSubmit();
                      return false;
                    }}>
                    <FormGroup className="mb-0">

                      <div className="mb-3 col-sm-3">
                            <Label>Plant Code</Label>
                            <Input
                              name="plantCd"
                              type="text"
                              disabled
                              maxLength={4}
                              onChange={app006p03ValidInput.handleChange}
                              value={app006p03ValidInput.values.plantCd || ""}
                              invalid={
                                app006p03ValidInput.touched.plantCd && app006p03ValidInput.errors.plantcd ? true : false
                              }
                            />
                            {app006p03ValidInput.touched.plantCd && app006p03ValidInput.errors.plantCd ? (
                              <FormFeedback type="invalid">{app006p03ValidInput.errors.plantCd}</FormFeedback>
                            ) : null}
                      </div>

                      <div className="mb-3 col-sm-3">
                            <Label>Plant Name</Label>
                            <Input
                              name="plantNm"
                              type="text"
                              maxLength={100}
                              onChange={app006p03ValidInput.handleChange}
                              value={app006p03ValidInput.values.plantNm || ""}
                              invalid={
                                app006p03ValidInput.touched.plantNm && app006p03ValidInput.errors.plantNm ? true : false
                              }
                            />
                            {app006p03ValidInput.touched.plantNm && app006p03ValidInput.errors.plantNm ? (
                              <FormFeedback type="invalid">{app006p03ValidInput.errors.plantNm}</FormFeedback>
                            ) : null}
                      </div>
                      
                      <div>
                        <Lov title="Company Code"
                          LenghtLabel=""
                          LenghtDiv="mb-3 col-sm-3"
                          columns={app006p03LovCompanyColumns}
                          getData={getCompany}
                          pageSize={10}
                          // callbackFunc = {null}
                          // onChangeFunc = {null}
                          defaultSetInput="companyCd"
                          invalidData={app006p03ValidInput}
                          fieldValue="companyCd"
                          stateSearchInput={app006p03LovCompany}
                          stateSearchInputSet={setApp006p03LovCompany}
                          touchedLovField={app006p03ValidInput.touched.companyCd}
                          errorLovField={app006p03ValidInput.errors.companyCd}
                        />
                      </div> 

                      <Button type="submit" color="primary" className="ms-1">
                          <i className="bx bxs-save align-middle me-2"></i>{" "}
                            Submit
                      </Button>
                      <Spinner style={{ display: app006p03Spinner ? "block" : "none", marginTop : '-35px'}} className="ms-4" color="danger" />
                    </FormGroup>
                    
                  </Form>
                        
                </CardBody>
              </Card>
            </Col>
          </Row>
          <div className="text-sm-end">
            <button
              type="button"
              className="btn btn-danger "
              onClick ={() => { setApp006p01Page(true); setApp006p03Page(false); setApp006setMsg("")}}
              >
              <i className="bx bx-arrow-back align-middle me-2"></i>{" "}
              Back
            </button>
          </div>
        </Container>

        
       </div>
    </React.Fragment>
  );
}

export default MasterPlant;