import { call, put, takeEvery, all} from "redux-saga/effects"

// Crypto Redux StatesgetMenuSuccess
import { EDIT_USER } from "./actionTypes"
import { msgEdit } from "./actions"

//Include Both Helper File with needed methods
import { editUser } from "helpers/backend_helper"


function* fetchEditUser({ payload: req }) {
  try {
    const response = yield call(editUser, req)
    yield put(msgEdit(response))
  } catch (error) {
    console.log(error);
    yield put(msgEdit({"status" : 0, "message" : "Error Get Data"}))
  }
}



function* editUserSaga() {
    
  yield takeEvery(EDIT_USER, fetchEditUser)

}

export default editUserSaga
