import React, { useState, useEffect } from "react";
import MetaTags from "react-meta-tags";  
import Breadcrumbs from "../../components/Common/Breadcrumb";
import BootstrapTable from "react-bootstrap-table-next";

import DeleteModal from "../../common/DeleteModal";
import paginationFactory from "react-bootstrap-table2-paginator";

import { useFormik, } from "formik";
import * as Yup from "yup";
import { getMstPositionData, editPosition, resetMessage, deletePosition, savePosition } from "../../store/app009/actions"


import {
  Row,
  Col,
  Card,
  CardBody,
  Container,
  Button,
  UncontrolledTooltip,
  Label,
  Input,
  FormFeedback,
  Form,
  Spinner,
  FormGroup,
  CardHeader,
  UncontrolledAlert,
} from "reactstrap";


 import { useSelector, useDispatch } from "react-redux"

const MasterPosition = () => {

  const dispatch = useDispatch();

  const [app009p01firstRenderDone, setApp009p01firstRenderDone] = useState(false);
  const [app009p01Page, setApp009p01Page] = useState(true)
  const [app009p02Page, setApp009p02Page] = useState(false)
  const [app009p03Page, setApp009p03Page] = useState(false)
  const [app009Msg, setApp009setMsg] = useState("")
  const [app009DeleteModal, setApp009DeleteModal] = useState(false);
  
  useEffect(() => {
    dispatch(resetMessage());
    
  }, [dispatch])

  const app009CloseAllert = () => {
    setApp009setMsg("")
  }

   //p01
   const [app009p01TabelSearch, setApp009p01TabelSearch] = useState('');
   const [app009p01TabelMasterPositionReq, setApp009p01TabelMasterPositionReq] = useState({page : 1, limit: 10, offset: 0, sort: "positionId", order: "asc", search: {any : app009p01TabelSearch}});
  
   const app009p01hendleTableChange = (type, {page, sortField, sortOrder, sizePerPage}) => {   
    if(type === "sort"){
      setApp009p01TabelMasterPositionReq({page : 1, limit: sizePerPage, offset: 0, sort: sortField, order: sortOrder, search: {any : app009p01TabelSearch}});
    }
    if(type==="pagination"){
      setApp009p01TabelMasterPositionReq({page : page, limit: sizePerPage, offset: ((page-1) * sizePerPage), sort: sortField, order: sortOrder, search: {any : app009p01TabelSearch}});
    }
  };

  useEffect(() => {
    setApp009p01firstRenderDone(true);
  }, []);

  useEffect(() => {
    if(app009p01firstRenderDone){
      dispatch(getMstPositionData(app009p01TabelMasterPositionReq))
    }
  }, [app009p01TabelMasterPositionReq])
  
  useEffect(() => {
    setApp009p01TabelMasterPositionReq({page : 1, limit: 10, offset: 0, sort: "positionId", order: "asc", search: {any : app009p01TabelSearch}});
  }, [app009p01TabelSearch])

  const app009p01MasterPositionData  = useSelector(state => {
    return state.MasterPositionReducer.respGetmPosition;
  });

  useEffect(() => {
    if(app009p01MasterPositionData.status == "0"){
      setApp009setMsg(app009p01MasterPositionData)
    }
  }, [app009p01MasterPositionData])

  useEffect(() => {
    if(app009p01firstRenderDone){
      dispatch(getMstPositionData(app009p01TabelMasterPositionReq))
    }
  }, [app009p01TabelMasterPositionReq])

  const app009p01TabelMasterPositionColumns = [
    {
      dataField: "positionId",
      text: "Position ID",
      sort: true,
      headerStyle: { textAlign: 'center' },
    },
    {
      dataField: "positionNm",
      text: "Position Name",
      sort: true,
      headerStyle: { textAlign: 'center' },
    },
    {
      dataField: "action",
      isDummyField: true,
      text: "Action",
      headerStyle: { textAlign: 'center' },
      formatter: (cellContent, app009p01MasterPositionData) => (
        <>
          <div style={{ justifyContent: 'center' }} className="d-flex gap-3">
            <i className="mdi mdi-pencil font-size-18  text-primary" id="edittooltip" onClick={() => app009p01PreEdit(app009p01MasterPositionData)} />
            <UncontrolledTooltip placement="top" target="edittooltip">
              Edit
            </UncontrolledTooltip>

            <i className="mdi mdi-delete font-size-18 text-danger" id="deletetooltip" onClick={() => app009p01Delete(app009p01MasterPositionData)} />
            <UncontrolledTooltip placement="top" target="deletetooltip">
              Delete
            </UncontrolledTooltip>

          </div>
        </>
      ),
    },
  ]

  const app009p01PreAdd = () => {
    app009p02ValidInput.setFieldValue("positionId", "")
    app009p02ValidInput.setFieldValue("positionNm", "")
    setApp009setMsg("")
    setApp009p01Page(false)
    setApp009p02Page(true)
  }

  const app009p01PreEdit = (app009p01MasterPositionData) => {
    setApp009setMsg("")
    app009p03ValidInput.setFieldValue("positionId", app009p01MasterPositionData.positionId)
    app009p03ValidInput.setFieldValue("positionNm", app009p01MasterPositionData.positionNm)
    setApp009p01Page(false)
    setApp009p03Page(true)
  }

  const [app009p01MasterPositionDelete, setApp009p01MasterPositionDelete] = useState(null);

  const app009p01Delete = (app009p01MasterPositionData) => {
    setApp009setMsg("")
    setApp009p01MasterPositionDelete(app009p01MasterPositionData);
    setApp009DeleteModal(true)
  }

  const  app009HandleDeleteMenu = () => {
    if (app009p01MasterPositionDelete.positionId) {
      dispatch(deletePosition(app009p01MasterPositionDelete));
      setApp009DeleteModal(false);
      setApp009p01MasterPositionDelete(null)
    }
  }

  const app009p01Message  = useSelector(state => {
    return state.MasterPositionReducer.msgDelete; 
  });

  useEffect(() => {
    if(app009p01Message.status == "1"){
      dispatch(getMstPositionData(app009p01TabelMasterPositionReq))
    }
    setApp009setMsg(app009p01Message);
  }, [app009p01Message])

//p02
const [app009p02Spinner, setApp009p02Spinner] = useState(false);

const app009p02ValidInput = useFormik({
  enableReinitialize: true,

  initialValues: {
    positionId: '',
    positionNm: '', 
  },
  validationSchema: Yup.object().shape({
    positionNm: Yup.string().required("Wajib diisi"),
  }),

  onSubmit: (values) => {
    setApp009p02Spinner(true);
    setApp009setMsg("")
    dispatch(savePosition(values));
  }
});

const app009p02Message = useSelector(state => {
  return state.MasterPositionReducer.msgAdd;
});

useEffect(() => {
  if (app009p02Message.status == "1") {
    dispatch(getMstPositionData(app009p01TabelMasterPositionReq))
    setApp009p01Page(true);
    setApp009p02Page(false);
  }
  setApp009setMsg(app009p02Message)
  setApp009p02Spinner(false);
}, [app009p02Message])

 //p03
 const [app009p03Spinner, setApp009p03Spinner] = useState(false);
 const app009p03ValidInput = useFormik({
   enableReinitialize: true,
 
   initialValues: {
     positionId: '',
     positionNm : '',
   },
   validationSchema: Yup.object().shape({
     positionId: Yup.string().required("Wajib diisi"),
     positionNm: Yup.string().required("Wajib diisi"),
   }),

   
   onSubmit: (values) => {
     setApp009p03Spinner(true);
     setApp009setMsg("")
     dispatch(editPosition(values));
   }
 });

 const app009p03Message  = useSelector(state => {
   return state.MasterPositionReducer.msgEdit;
 });

 useEffect(() => {
   if(app009p03Message.status == "1"){
     setApp009p01Page(true); 
     setApp009p03Page(false);
     dispatch(getMstPositionData(app009p01TabelMasterPositionReq))
   }
   setApp009setMsg(app009p03Message)
   setApp009p03Spinner(false);
 }, [app009p03Message])


  return (
    <React.Fragment>
      
      <div className="page-content">
        <MetaTags>
          <title>
          Korindo App
          </title>
        </MetaTags>

        <DeleteModal
          show={app009DeleteModal}
          onDeleteClick={app009HandleDeleteMenu}
          onCloseClick={() => setApp009DeleteModal(false)}
        />

        {app009Msg !== ""? <UncontrolledAlert toggle={app009CloseAllert} color={app009Msg.status == "1" ? "success" : "danger"}>
                  {typeof app009Msg == 'string' ? app009Msg : app009Msg.listmessage?.map((msg, key) => ( <p key={key}>{"* " +msg}</p>))}</UncontrolledAlert> : null}

        <Container style={{display : app009p01Page ? 'block' : 'none'}} fluid={true}>
          <Breadcrumbs title="Forms" breadcrumbItem="Master Position" />
          
           <Row>
            <Col>
              <Card>
              <CardHeader><i className="bx bx-list-check font-size-18 align-middle me-2"></i>List Position</CardHeader>
                 <CardBody>
                 <React.Fragment>
                             <Row className="mb-2">
                               <Col sm="2">
                                 <div className="form-group m-0">
                                   <div className="input-group">
                                   <input
                                       type="text"
                                       className="form-control"
                                       placeholder="Search ..."
                                       value={app009p01TabelSearch}
                                       onChange={ e  => {setApp009p01TabelSearch(e.target.value);} } 
                                     />
                                     <div className="input-group-append">
                                       <button className="btn btn-dark" type="button" 
                                          onClick={() => {
                                            dispatch(getMstPositionData(app009p01TabelMasterPositionReq))
                                                         }}>
                                         <i className="mdi mdi-magnify" />
                                       </button>
                                     </div>
                                   </div>
                                 </div>
                               </Col>
                               <Col sm="10">
                                 <div className="text-sm-end">
                                 <button
                                       type="button"
                                       className="btn btn-primary "
                                        onClick= {() =>  { app009p01PreAdd()}}
                               >  
                                   <i className="bx bx-plus font-size-16 align-middle me-2"></i>{" "}
                                   Add 
                                 </button>
                                 </div>
                               </Col>
                             </Row>
                           <Row>
                            
                           <BootstrapTable
                              wrapperClasses="table-responsive"
                              keyField = "positionId"
                              rowClasses="text-nowrap"
                              remote ={{ filter: true, pagination: true, sort: true, cellEdit: true }}
                              data = {app009p01MasterPositionData.data != null ? app009p01MasterPositionData.data.position : []}
                              
                              columns = {app009p01TabelMasterPositionColumns}
                              pagination = {paginationFactory ({
                                  page : app009p01TabelMasterPositionReq.page,
                                  sizePerPage : app009p01TabelMasterPositionReq.limit,
                                  sizePerPageList : [5, 10,20],
                                  totalSize : app009p01MasterPositionData.data != null ? app009p01MasterPositionData.data.positiontotal : 0,
                                  showTotal: true,
                              })}
                              classes={
                                "table align-middle table-nowrap"
                              }
                              onTableChange = {app009p01hendleTableChange}
                              striped
                              hover
                              condensed
                            />

                           </Row>
                  </React.Fragment>
                </CardBody>
              </Card>
            </Col>
          </Row>
        </Container>

        {/* //p02 ADD */}
        <Container style={{ display: app009p02Page ? 'block' : 'none' }} fluid={true}>
          <Breadcrumbs title="Forms" breadcrumbItem="Add Master Position" />

          <Row>
            <Col lg={12}>
              <Card>
                <CardHeader><i className="bx bx-add-to-queue font-size-18 align-middle me-2"></i> Add Master Position</CardHeader>
                <CardBody>
                  <Form
                    onSubmit={(e) => {
                      e.preventDefault();
                      app009p02ValidInput.handleSubmit();
                      return false;
                    }}>
                    <FormGroup className="mb-0">
                      <div className="mb-3 col-sm-3">
                        <Label>Position ID</Label>
                        <Input
                          name="positionId"
                          type="text"
                          disabled
                          value={app009p02ValidInput.values.positionId || ""}
                        />
                      </div>
                      
                      <div className="mb-3 col-sm-3">
                        <Label>Position Name</Label>
                        <Input
                          name="positionNm"
                          type="text"
                          maxLength={50}
                          onChange={app009p02ValidInput.handleChange}
                          value={app009p02ValidInput.values.positionNm || ""}
                          invalid={
                            app009p02ValidInput.touched.positionNm && app009p02ValidInput.errors.positionNm ? true : false
                          }
                        />
                        {app009p02ValidInput.touched.positionNm && app009p02ValidInput.errors.positionNm ? (
                          <FormFeedback type="invalid">{app009p02ValidInput.errors.positionNm}</FormFeedback>
                        ) : null}
                      </div>
            
                      <Button type="submit" color="primary" className="ms-1">
                        <i className="bx bxs-save align-middle me-2"></i>{" "}
                        Submit
                      </Button>
                      <Spinner style={{ display: app009p02Spinner ? "block" : "none", marginTop: '-35px' }} className="ms-4" color="danger" />
                    </FormGroup>

                  </Form>

                </CardBody>
              </Card>
            </Col>
          </Row>
          <div className="text-sm-end">
            <button
              type="button"
              className="btn btn-danger "
              onClick={() => { setApp009p01Page(true); setApp009p02Page(false); setApp009setMsg("") }}
            >
              <i className="bx bx-arrow-back align-middle me-2"></i>{" "}
              Back
            </button>
          </div>
        </Container>


        {/* //p03 */}
        <Container style={{display : app009p03Page ? 'block' : 'none'}} fluid={true}>
          <Breadcrumbs title="Forms" breadcrumbItem="Master Position" />
                              
          <Row>
            <Col lg={12}>
              <Card>
                <CardHeader><i className="bx bxs-edit-alt font-size-18 align-middle me-2"></i>Edit Master Position</CardHeader>
                <CardBody>
                  <Form
                    onSubmit={(e) => {
                      e.preventDefault();
                      app009p03ValidInput.handleSubmit();
                      return false;
                    }}>
                    <FormGroup className="mb-0">

                      <div className="mb-3 col-sm-3">
                            <Label>Position ID</Label>
                            <Input
                              name="positionId"
                              type="text"
                              disabled
                              onChange={app009p03ValidInput.handleChange}
                              value={app009p03ValidInput.values.positionId || ""}
                              invalid={
                                app009p03ValidInput.touched.positionId && app009p03ValidInput.errors.positionId ? true : false
                              }
                            />
                            {app009p03ValidInput.touched.positionId && app009p03ValidInput.errors.positionId ? (
                              <FormFeedback type="invalid">{app009p03ValidInput.errors.positionId}</FormFeedback>
                            ) : null}
                      </div>

                      <div className="mb-3 col-sm-3">
                            <Label>Position Name</Label>
                            <Input
                              name="positionNm"
                              type="text"
                              maxLength={100}
                              onChange={app009p03ValidInput.handleChange}
                              value={app009p03ValidInput.values.positionNm || ""}
                              invalid={
                                app009p03ValidInput.touched.positionNm && app009p03ValidInput.errors.positionNm ? true : false
                              }
                            />
                            {app009p03ValidInput.touched.positionNm && app009p03ValidInput.errors.positionNm ? (
                              <FormFeedback type="invalid">{app009p03ValidInput.errors.positionNm}</FormFeedback>
                            ) : null}
                      </div>
                      
                      <Button type="submit" color="primary" className="ms-1">
                          <i className="bx bxs-save align-middle me-2"></i>{" "}
                            Submit
                      </Button>
                      <Spinner style={{ display: app009p03Spinner ? "block" : "none", marginTop : '-35px'}} className="ms-4" color="danger" />
                    </FormGroup>
                    
                  </Form>
                        
                </CardBody>
              </Card>
            </Col>
          </Row>
          <div className="text-sm-end">
            <button
              type="button"
              className="btn btn-danger "
              onClick ={() => { setApp009p01Page(true); setApp009p03Page(false); setApp009setMsg("")}}
              >
              <i className="bx bx-arrow-back align-middle me-2"></i>{" "}
              Back
            </button>
          </div>
        </Container>

        
       </div>
    </React.Fragment>
  );
}

export default MasterPosition;